import ReportParamDetailModalStore from "../store/ReportParamDetailModalStore";

export enum ReportParamDetailModalActionType {
  OPEN_MODAL = "open_modal",
  CLOSE_MODAL = "close_modal",
}

export interface ReportParamDetailModalAction {
  type: ReportParamDetailModalActionType;
  content: any;
}

const initState = {
  visible: false,
  paramType: "",
  paramStringValue: "",
};

function openModal(
  paramDetail: {
    paramType: string;
    paramValue: string;
  },
  storeState: ReportParamDetailModalStore
) {
  return {
    ...storeState,
    visible: true,
    paramType: paramDetail.paramType,
    paramStringValue: paramDetail.paramValue,
  };
}

function closeModal(storeState: ReportParamDetailModalStore) {
  return { ...storeState, visible: false, paramType: "", paramStringValue: "" };
}

export function reportParamDetailModalReducer(
  state: ReportParamDetailModalStore = initState,
  action: ReportParamDetailModalAction
) {
  switch (action.type) {
    case ReportParamDetailModalActionType.OPEN_MODAL:
      return openModal(action.content, state);
    case ReportParamDetailModalActionType.CLOSE_MODAL:
      return closeModal(state);
    default:
      return state;
  }
}
