import { ClockCircleOutlined } from '@ant-design/icons';
import { useEffect, useState } from "react";
import { Button, Col, Collapse, Divider, Row, Table, Typography } from "antd";

import ReportObject from "../../common/object/ReportObject";
import ReportType from "../../common/object/ReportType";
import { ResultParam } from "../../common/object/ResultParam";
import StepResultTableRow from "../../common/object/table_row_data/StepResultTableRow";
import ResultDetailInfoExpandedRow from "../step_result_table/ResultDetailInfoExpandedRow";
import { StepResultTableColumnName } from "../step_result_table/StepResultTableColumnName";
import TableHeader from "../step_result_table/TableHeader";
import StepResultTableColRenderSupport2 from "./StepResultTableColRenderSupport2";
import SubResultsTable from "../subresults_card/SubResultsTable";

interface StepResultTableProp {
  stepResultList: ReportObject[];
  stepIdx: string;
}

export default function StepResultTable2(prop: StepResultTableProp) {
  const [columns, setColumns] = useState<any[]>([]);
  const [toggleTime, setToggleTime] = useState<boolean>(false);

  const components = {
    header: {
      cell: TableHeader,
    },
  };

  const checkHasInfoToShow = (result: ReportObject) => {
    if (result.meta.outputResult !== undefined || result.meta.error || result.meta.imageName) return true;
    return false;
  };

  const checkRowCanExpand = (row: StepResultTableRow) => {
    const resultObj = row.result as ReportObject;
    if (resultObj.results !== undefined && resultObj.results.length > 0) return true;
    if (resultObj.subResults !== undefined && resultObj.subResults.length > 0) return true;
    if (checkHasInfoToShow(row.result as ReportObject)) return true;
    return false;
  };

  
  const onToggleTime = () => {
    setToggleTime(!toggleTime);
  };

  const generateData = (resultList: ReportObject[], prefixKey: string) => {
    const returnData: StepResultTableRow[] = [];
    let idx = 1;
    resultList.forEach((result) => {
      if (result.typeName === ReportType.NOTEXECUTE) {
        idx += 1;
        return;
      }

      const isForRound =
        result.typeName === ReportType.ROUNDDATA ||
        result.typeName === ReportType.ROUNDINDEX;
      if (prefixKey === "")
        returnData.push(new StepResultTableRow(idx.toString(), result));
      else
        returnData.push(
          new StepResultTableRow(
            isForRound ? `${prefixKey}[${idx}]` : `${prefixKey}.${idx}`,
            result
          )
        );
      idx += 1;
    });
    return returnData;
  };

  const columnsTemplate = [
    { id: StepResultTableColumnName.NAME, title: StepResultTableColumnName.NAME, dataIndex: 'name', ellipsis: true, width: 150 },
    {
      id: StepResultTableColumnName.PLATFORM,
      title: StepResultTableColumnName.PLATFORM,
      dataIndex: 'platform',
      ellipsis: true,
      width: 150,
    },
    {
      id: StepResultTableColumnName.PARAMETER,
      title: StepResultTableColumnName.PARAMETER,
      dataIndex: 'param',
      ellipsis: true,
      width: 150,
    },
    { id: StepResultTableColumnName.STATUS, title: StepResultTableColumnName.STATUS, dataIndex: 'status', ellipsis: true, width: 150 },
    {
      id: StepResultTableColumnName.TIME,
      title: (
        <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between' }}>
          {StepResultTableColumnName.TIME}
          <Button type="text" size="small" onClick={onToggleTime} icon={<ClockCircleOutlined />} />
        </div>
      ),
      dataIndex: 'time',
      ellipsis: true,
      width: 150,
    },
  ];

  useEffect(() => {
    if (columns.length === 0) {
      setColumns(columnsTemplate);
    } else {
      setColumns(
        columnsTemplate.map((column, index) => {
          return {
            ...column,
            width: columns[index].width,
          };
        })
      );
    }
  }, [prop.stepResultList, toggleTime]);

  const handleResize = (index: number) => (width: any) => {
    const newColumn = [...columns];
    newColumn[index].width = width;
    setColumns(newColumn);
  };

  const finalColumn = columns.map((col, index) => {
    const colRender = new StepResultTableColRenderSupport2(col.id);
    return {
      ...col,
      render: colRender.getColRender(toggleTime),
      onHeaderCell: (column: any) => ({
        width: column.width,
        onResize: handleResize(index),
      }),
    };
  });

  const data = generateData(prop.stepResultList, prop.stepIdx);

  const rowExpandRender = (
    record: StepResultTableRow,
    _index: any,
    _indent: any,
    expanded: boolean
  ) => {
    if (!expanded) {
      return null;
    }

    const resultObj = record.result as ReportObject;
    const isKwGrpStep = record.result.typeName === ReportType.GROUP;

    return (
      <>
        {checkHasInfoToShow(resultObj) ? (
          <Row>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Info</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <ResultDetailInfoExpandedRow output={resultObj.meta.outputResult as ResultParam | undefined} failureCause={resultObj.meta.error as string | undefined} imageName={resultObj.meta.imageName as string | undefined} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}

        {resultObj.subResults !== undefined && resultObj.subResults.length > 0 ? (
          <Row style={{ marginTop: '20px' }}>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Sub Step</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <SubResultsTable results={resultObj.subResults} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}

        {resultObj.results !== undefined && resultObj.results.length > 0 ? (
          <Row style={{ marginTop: '20px' }}>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Child Execution</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <StepResultTable2 stepResultList={resultObj.results} stepIdx={isKwGrpStep ? '' : record.key} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}
      </>
    );
  };

  return (
    <Table
      rowKey="key"
      columns={finalColumn}
      dataSource={data}
      expandable={{
        expandedRowRender: rowExpandRender,
        rowExpandable: checkRowCanExpand,
      }}
      pagination={{ hideOnSinglePage: true }}
      components={components}
      className="custom-table report-table"
    />
  );
}
