import { Col, Collapse, Divider, Row, Table, Typography } from 'antd';
import { useEffect, useState } from 'react';
import ReportObject from '../../common/object/ReportObject';
import ReportStatus from '../../common/object/ReportStatus';
import ReportType from '../../common/object/ReportType';
import StepResultTableRow from '../../common/object/table_row_data/StepResultTableRow';
import TableHeader from '../step_result_table/TableHeader';
import TestFlexTableColRenderSupport2 from './TestFlexTableColRenderSupport2';
import SubResultsTable from '../subresults_card/SubResultsTable';


type TestFlexReportTable2Prop = {
  resultList: ReportObject[];
  stepIdx: string;
};

export default function TestFlexReportTable2(prop: TestFlexReportTable2Prop) {
  const [columns, setColumns] = useState<any[]>([]);

  const components = {
    header: {
      cell: TableHeader,
    },
  };

  const checkHasInfoToShow = (result: ReportObject) => {
    if (result.meta.outputResult !== undefined || result.meta.error || result.meta.imageName) return true;
    return false;
  };

  const checkRowCanExpand = (row: StepResultTableRow) => {
    const resultObj = row.result as ReportObject;
    if (resultObj.results !== undefined && resultObj.results.length > 0) return true;
    if (resultObj.subResults !== undefined && resultObj.subResults.length > 0) return true;
    if (checkHasInfoToShow(row.result as ReportObject)) return true;
    return false;
  };

  const checkShouldShowChildResult = (reportObj: ReportObject) => {
    switch (reportObj.typeName) {
      case ReportType.IF:
      case ReportType.FORDATA:
      case ReportType.FORINDEX:
      case ReportType.IF_PASS:
      case ReportType.IF_PASS_SECTION:
        return true;
      default:
        return false;
    }
  };

  const generateData = (resultList: ReportObject[], prefixKey: string) => {
    const returnData: StepResultTableRow[] = [];
    let idx = 1;
    resultList.forEach((result) => {
      if (result.typeName === ReportType.NOTEXECUTE && result.status !== ReportStatus.FAIL) {
        idx += 1;
        return;
      }

      const isForRound = result.typeName === ReportType.ROUNDDATA || result.typeName === ReportType.ROUNDINDEX;
      if (prefixKey === '') returnData.push(new StepResultTableRow(idx.toString(), result));
      else returnData.push(new StepResultTableRow(isForRound ? `${prefixKey}[${idx}]` : `${prefixKey}.${idx}`, result));
      idx += 1;
    });
    return returnData;
  };

  const columnsTemplate = [
    {
      title: 'Name',
      dataIndex: 'name',
      ellipsis: true,
      width: 150,
    },
    {
      title: 'Platform',
      dataIndex: 'platform',
      ellipsis: true,
      width: 150,
    },
    {
      title: 'Input Data',
      dataIndex: 'param',
      ellipsis: true,
      width: 150,
    },
    {
      title: 'Status',
      dataIndex: 'status',
      ellipsis: true,
      width: 150,
    },
    {
      title: 'Execute Time',
      dataIndex: 'time',
      ellipsis: true,
      width: 150,
    },
  ];

  useEffect(() => {
    if (columns.length === 0) {
      setColumns(columnsTemplate);
    } else {
      setColumns(
        columnsTemplate.map((column, index) => {
          return {
            ...column,
            width: columns[index].width,
          };
        })
      );
    }
  }, [prop.resultList]);

  const handleResize = (index: number) => (width: any) => {
    const newColumn = [...columns];
    newColumn[index].width = width;
    setColumns(newColumn);
  };

  const finalColumn = columns.map((col, index) => {
    const colRender = new TestFlexTableColRenderSupport2(col.title);
    return {
      ...col,
      render: colRender.getColRender(),
      onHeaderCell: (column: any) => ({
        width: column.width,
        onResize: handleResize(index),
      }),
    };
  });

  const data = generateData(prop.resultList, prop.stepIdx);

  const rowExpandRender = (record: StepResultTableRow, _index: any, _indent: any, expanded: boolean) => {
    if (!expanded) {
      return null;
    }

    const resultObj = record.result as ReportObject;

    return (
      <>
        {resultObj.subResults !== undefined && resultObj.subResults.length > 0 ? (
          <Row style={{ marginTop: '20px' }}>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Sub Step</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <SubResultsTable results={resultObj.subResults} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}

        {resultObj.results !== undefined && resultObj.results.length > 0 && checkShouldShowChildResult(resultObj) ? (
          <Row style={{ marginTop: '20px' }}>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Child Item</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <TestFlexReportTable2 resultList={record.result.results as ReportObject[]} stepIdx={record.key} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}
      </>
    );
  };

  return (
    <Table
      rowKey="key"
      columns={finalColumn}
      dataSource={data}
      expandable={{
        expandedRowRender: rowExpandRender,
        rowExpandable: checkRowCanExpand,
      }}
      pagination={{ hideOnSinglePage: true }}
      components={components}
      className="custom-table report-table"
    />
  );
}
