import PathInfo from "../PathInfo";
import ReportObject from "../ReportObject";
import ReportType from "../ReportType";
import ResultObject from "../ResultObject";
import { ResultType } from "../ResultType";
import PathStore from "../store/PathStore";

export enum PathActionType {
  APPEND_PATH = "append_path",
  MOVE_TO_EXIST_PATH = "move_to_exist_path",
  SET_REPORT_PATH = 'set_report_path',
  GO_BACK = "go_back",
}

export interface PathAction {
  type: PathActionType;
  content: any;
}

const initState = {
  pathInfo: [],
  visitedRs: [],
};

function generateDisplayName(resultObject: ResultObject | ReportObject, preferDisplayName?: string) {
  switch (resultObject.typeName) {
    case ReportType.TESTFLEX:
      return `Test Flex ${preferDisplayName ? preferDisplayName : resultObject.name}`;
    case ResultType.TESTPLAN:
      return `Test Plan ${preferDisplayName ? preferDisplayName : resultObject.name}`;
    case ResultType.TESTSUITE:
    case ReportType.TESTSUITE:
    case ReportType.FLEX_TS:
      return `Test Suite ${preferDisplayName ? preferDisplayName :resultObject.name}`;
    case ResultType.TESTCASE:
    case ReportType.TESTCASE:
    case ReportType.JMTESTCASE:
    case ReportType.FLEX_TC:
      return `Test Case ${preferDisplayName ? preferDisplayName :resultObject.name}`;
    default:
      return resultObject.name;
  }
}

function appendPath(
  pathInfo: { fullIndexToPath: number[]; resultObject: ResultObject | ReportObject, preferDisplayName?: string },
  storeState: PathStore
) {
  const resultId = pathInfo.fullIndexToPath.join('-');
  if (storeState.pathInfo.length === 0) {
    const path = new PathInfo(
      generateDisplayName(pathInfo.resultObject, pathInfo.preferDisplayName),
      pathInfo.fullIndexToPath,
      resultId
    );
    return { ...storeState, pathInfo: [path], visitedRs: [resultId] };
  } else {
    const path = new PathInfo(
      generateDisplayName(pathInfo.resultObject, pathInfo.preferDisplayName),
      pathInfo.fullIndexToPath,
      resultId
    );
    const alreadyVisited = storeState.visitedRs.includes(resultId);
    return { ...storeState, pathInfo: [...storeState.pathInfo, path], visitedRs: alreadyVisited ? storeState.visitedRs : [...storeState.visitedRs, resultId] };
  }
}

function moveToPath(resourceId: string, storeState: PathStore) {
  let idx = storeState.pathInfo.length;
  storeState.pathInfo.forEach((path, index) => {
    if (path.resourceId === resourceId) {
      idx = index;
    }
  });
  const alreadyVisited = storeState.visitedRs.includes(resourceId);
  return { ...storeState, pathInfo: storeState.pathInfo.slice(0, idx + 1), visitedRs: alreadyVisited ? storeState.visitedRs : [...storeState.visitedRs, resourceId] };
}

function goBack(storeState: PathStore) {
  if (storeState.pathInfo.length > 1) {
    return moveToPath(storeState.pathInfo[storeState.pathInfo.length - 1].resourceId, storeState)
  }
  return storeState;
}

function setReportPath(pathInfos: { fullIndexToPath: number[]; resultObject: ResultObject, preferDisplayName?: string }[], storeState: PathStore) {
  const newPathInfo = [storeState.pathInfo[0]];
  pathInfos.forEach((info) => {
    const resultId = info.fullIndexToPath.join('-');
    newPathInfo.push(new PathInfo(generateDisplayName(info.resultObject, info.preferDisplayName), info.fullIndexToPath, resultId));
  });
  const lastPathInfo = newPathInfo[newPathInfo.length - 1];
  const alreadyVisited = storeState.visitedRs.includes(lastPathInfo.resourceId);
  return {
    ...storeState,
    pathInfo: newPathInfo,
    visitedRs: alreadyVisited ? storeState.visitedRs : [...storeState.visitedRs, lastPathInfo.resourceId],
  };
}

export function pathReducer(state: PathStore = initState, action: PathAction) {
  switch (action.type) {
    case PathActionType.APPEND_PATH:
      return appendPath(action.content, state);
    case PathActionType.MOVE_TO_EXIST_PATH:
      return moveToPath(action.content, state);
    case PathActionType.GO_BACK:
      return goBack(state);
    case PathActionType.SET_REPORT_PATH:
      return setReportPath(action.content.pathInfos, state);
    default:
      return state;
  }
}
