import { Col, Row, Select, Form, Input, Button, Table, Divider } from "antd";
import { useForm } from "antd/lib/form/Form";
import { useEffect, useState } from "react";
import ReportObject from "../../common/object/ReportObject";
import ReportStatus from "../../common/object/ReportStatus";
import { ResultParam } from "../../common/object/ResultParam";
import SummaryTestCaseTableRow from "../../common/object/table_row_data/SummaryTestCaseTableRow";
import TestCaseRoundTableRow from "../../common/object/table_row_data/TestCaseRoundTableRow";
import { getColorForReportStatus, getIconForReport2Status, convertTimeToString } from "../../common/utilities/ReportUtilities";
import TableCellTestNameTitle from "../common/title/TableCellTestNameTitle";
import StepResultComponent from "../test_case_summary_table/StepResultComponent";

const { Option } = Select;

interface TestCaseRoundComponentProp {
  row: SummaryTestCaseTableRow;
  pathToResult: number[];
}

export default function TestCaseRoundComponent2(
  prop: TestCaseRoundComponentProp
) {
  const [form] = useForm();
  const [dataTable, setDataTable] = useState<TestCaseRoundTableRow[]>([]);
  const [filteredDataTable, setFilteredDataTable] = useState<
    TestCaseRoundTableRow[]
  >([]);
  const [statusFilter, setStatusFilter] = useState<
    { text: string; value: ReportStatus }[]
  >([]);

  useEffect(() => {
    const rows: TestCaseRoundTableRow[] = [];
    const statusFilterArr: any[] = [];
    const statusSet = new Set<ReportStatus>();

    ((prop.row.resultObj as ReportObject).results as ReportObject[]).forEach(
      (val, index) => {
        if (val.status === ReportStatus.NOTEXECUTE) return;

        rows.push(new TestCaseRoundTableRow(index + 1, val));
        statusSet.add(val.status);
      }
    );
    statusSet.forEach((status) => {
      statusFilterArr.push({ text: status, value: status });
    });

    setDataTable(rows);
    setStatusFilter(statusFilterArr);
    setFilteredDataTable(rows);
  }, [prop.row]);

  const columns = [
    {
      title: "Round",
      key: "round",
      render: (_: string, row: TestCaseRoundTableRow) => {
        return (
          <TableCellTestNameTitle
            indexToPath={[
              ...prop.pathToResult,
              (row.resultObj as ReportObject).meta.rsIndex as number,
            ]}
            resultObject={row.resultObj}
            text={row.roundName}
            preferDisplayName={`${prop.row.caseName} (${row.roundName})`}
          />
        );
      },
    },
    {
      title: "Status",
      key: "status",
      onFilter: (value: any, record: TestCaseRoundTableRow) =>
        record.status === value,
      filters: statusFilter,
      render: (_: string, row: TestCaseRoundTableRow) => (
        <div>
          <span
            style={{
              marginBottom: 0,
              color: getColorForReportStatus(row.status as ReportStatus),
            }}
          >
            {" "}
            {getIconForReport2Status(row.status as ReportStatus, 14)}
          </span>
          <span
            style={{
              marginLeft: "5px",
              color: getColorForReportStatus(row.status as ReportStatus),
            }}
          >
            {row.status}
          </span>
        </div>
      ),
    },
    {
      title: "Execute Time",
      key: "executeTime",
      render: (_: string, row: any) => {
        return <div>{convertTimeToString(row.executeTime)}</div>;
      },
    },
  ];

  const handleSubmitForm = (value: any) => {
    if (value.select !== "all") {
      const newDataTable = dataTable.filter((res) => {
        const newArr = (
          (res.resultObj as ReportObject).meta.params as ResultParam[]
        ).find((param) => {
          if (value.value === "") {
            return param.paramName === value.select;
          }
          return (
            param.paramName === value.select &&
            param.displayValue === value.value
          );
        });
        return newArr;
      });
      setFilteredDataTable(newDataTable);
    } else {
      setFilteredDataTable(dataTable);
    }
  };

  const handleChangeSelect = () => {
    form.setFieldsValue({ value: "" });
  };

  const paramNameArr = (
    (prop.row.resultObj as ReportObject).results as ReportObject[]
  )
    .map((result) =>
      (result.meta.params as ResultParam[]).map((param) => param.paramName)
    )
    .flat()
    .filter((val, index, arr) => arr.indexOf(val) === index);

  return (
    <>
      <Row>
        <Divider>Round Filter</Divider>
        <Form
          form={form}
          layout="inline"
          onFinish={handleSubmitForm}
          style={{ width: "100%", paddingBottom: "10px" }}
        >
          <Col span={10}>
            <Form.Item name="select" initialValue="all">
              <Select onChange={handleChangeSelect}>
                <Option value="all">Get All</Option>
                {paramNameArr.map((name) => (
                  <Option key={name} value={name}>
                    {name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={10}>
            <Form.Item name="value" initialValue="">
              <Input placeholder="Input Value" />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item>
              <Button
                htmlType="submit"
                type="primary"
                style={{ width: "100%" }}
              >
                Filter
              </Button>
            </Form.Item>
          </Col>
        </Form>
      </Row>
      <Row>
        <Divider>Round Table</Divider>
        <Table
          style={{ width: "100%" }}
          columns={columns}
          expandable={{
            expandedRowRender: (record) => (
              <StepResultComponent
                params={
                  (record.resultObj as ReportObject).meta
                    .params as ResultParam[]
                }
              />
            ),
          }}
          dataSource={filteredDataTable}
          pagination={{ hideOnSinglePage: true }}
        />
      </Row>
    </>
  );
}
