import React from "react";
import { Descriptions, Typography } from "antd";

import ShadowRoot from '../../../shadow-root/ShadowRoot';
import { convertTimeToString } from "../../../../../common/utilities/ReportUtilities";

const { Paragraph } = Typography;

interface ResponseObjectDetailProp {
  objJson: string;
}

export default function ResponseObjectDetail(prop: ResponseObjectDetailProp) {
  let responseObj: any;

  try {
    responseObj = JSON.parse(prop.objJson);
  } catch (e) {
    console.log(e);
  }

  if (responseObj === undefined) return null;

  const getColorForStatus = (statusCode: number) => {
    if (statusCode >= 100 && statusCode < 200) {
      return '#0fa9ea';
    }
    if (statusCode >= 200 && statusCode < 300) {
      return '#09af00';
    }
    if (statusCode >= 300 && statusCode < 400) {
      return '#fea101';
    }
    if (statusCode >= 400 && statusCode < 600) {
      return '#f01e23';
    }
    return 'black';
  };

  const headerMap = Object.keys(responseObj.responseHeaders);

  return (
    <>
      <Descriptions title="Response Info" column={1} bordered>
        <Descriptions.Item label="Status" style={{ color: getColorForStatus(responseObj.statusCode) }}>{`${responseObj.statusCode} ${responseObj.reasonPhrase}`}</Descriptions.Item>
        <Descriptions.Item label="Response Time">{convertTimeToString(responseObj.requestTime)}</Descriptions.Item>
        <Descriptions.Item label="Size">{`${responseObj.responseBodySize} bytes`}</Descriptions.Item>
      </Descriptions>
      <Descriptions title="Request Header" style={{ marginTop: '20px' }} column={1} bordered>
        {headerMap.map((key) => (
          <Descriptions.Item label={key} key={key}>
            <Paragraph style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} copyable ellipsis={{ expandable: true, symbol: 'See more', rows: 1 }}>
              {responseObj.responseHeaders[key]}
            </Paragraph>
          </Descriptions.Item>
        ))}
      </Descriptions>
      <Descriptions title={<Typography.Text copyable={{ text: responseObj.responseBody }}>Response Body</Typography.Text>} style={{ marginTop: '20px' }} column={1} bordered>
        <Descriptions.Item>
          <ShadowRoot>
            <div
              // eslint-disable-next-line react/no-danger
              dangerouslySetInnerHTML={{ __html: responseObj.responseBody }}
            />
          </ShadowRoot>
        </Descriptions.Item>
      </Descriptions>
    </>
  );
}
