import React from "react";
import { Typography } from 'antd';
import { useSelector } from 'react-redux';
import { checkResultVisited } from '../../../common/object/selector/PathSelector';

interface ResultTreeTitleProp {
  text: string;
  resultId: string;
}

export default function ResultTreeTitle(prop: ResultTreeTitleProp) {
  const isVisited = useSelector(checkResultVisited(prop.resultId));
  return isVisited ? (
    <Typography.Text type="secondary" style={{ marginLeft: 5, whiteSpace: 'nowrap', textOverflow: 'ellipsis' }} title={prop.text}>
      {prop.text}
    </Typography.Text>
  ) : (
    <Typography.Text style={{ marginLeft: 5, whiteSpace: 'nowrap', textOverflow: 'ellipsis' }} title={prop.text} strong>
      {prop.text}
    </Typography.Text>
  );
}
