import StepResultTableRow from "../../common/object/table_row_data/StepResultTableRow";
import { StepResultTableColumnName } from "../step_result_table/StepResultTableColumnName";
import StepResultTableCellRenderSupport2 from "./StepResultTableCellRenderSupport2";

export default class StepResultTableColRenderSupport2 {
  col: StepResultTableColumnName;
  cellRenderSupport: StepResultTableCellRenderSupport2;

  constructor(col: StepResultTableColumnName) {
    this.col = col;
    this.cellRenderSupport = new StepResultTableCellRenderSupport2(col);
  }

  getColRender(toggleTime:boolean) {
    switch (this.col) {
      case StepResultTableColumnName.NAME:
      case StepResultTableColumnName.PARAMETER:
      case StepResultTableColumnName.PLATFORM:
      case StepResultTableColumnName.STATUS:
      case StepResultTableColumnName.TIME:
        return (_text: string, row: StepResultTableRow) => {
          return this.cellRenderSupport.getCellRender(row, toggleTime);
        };
      default:
        return undefined;
    }
  }
}
