import React from "react";
import { Descriptions, Typography, Image } from "antd";

import { ReportParamDetailModalActionType } from "../../common/object/reducer/RerportParamDetailModalReducer";
import { store } from "../../common/store";
import { ResultParam } from "../../common/object/ResultParam";
import { hasCopyValue } from "../../common/utilities/ReportUtilities";

const { Paragraph } = Typography;

interface ResultDetailExpandedRowProp {
  output?: ResultParam;
  failureCause?: string;
  imageName?: string;
}

export default function ResultDetailInfoExpandedRow(
  prop: ResultDetailExpandedRowProp
) {
  const generateOutputValue = (output: ResultParam) => {
    switch (output.paramType) {
      case "UIObject":
      case "Web UIObject":
      case "Mobile UIObject":
      case "Windows UIObject":
      case "TestDataObject":
      case "Data Slice":
      case "WebServiceObject":
      case "DatabaseQueryObject":
      case "ResponseObject":
      case "ValueMap":
        return (
          // eslint-disable-next-line jsx-a11y/anchor-is-valid
          <a
            onClick={() => {
              store.dispatch({
                type: ReportParamDetailModalActionType.OPEN_MODAL,
                content: {
                  paramType: output.paramType,
                  paramValue: output.detailValue,
                },
              });
            }}
          >
            {output.displayValue}
          </a>
        );
      case "Image":
        return (
          <Image
            width={200}
            src={`data:image/png;base64,${output.detailValue}`}
          />
        );
      default:
        return output.displayValue;
    }
  };

  return (
    <Descriptions column={1} bordered>
      {prop.output !== undefined && (
        <Descriptions.Item label="Output">
          <Paragraph style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} ellipsis={{ rows: 3, expandable: true, symbol: 'See more' }} copyable={hasCopyValue(prop.output) ? { text: prop.output.displayValue, tooltips: false } : undefined}>
            {generateOutputValue(prop.output)}
          </Paragraph>
        </Descriptions.Item>
      )}
      {prop.failureCause !== undefined && prop.failureCause !== '' && (
        <Descriptions.Item label="Error Log">
          <Paragraph code style={{ whiteSpace: "pre-wrap", marginBottom: 0 }} copyable>
            {prop.failureCause}
          </Paragraph>
        </Descriptions.Item>
      )}
      {prop.imageName !== undefined && (
        <Descriptions.Item label="Image">
          <Image width={200} src={`./image/${prop.imageName}`} />
        </Descriptions.Item>
      )}
    </Descriptions>
  );
}
