import { Card, Col, Descriptions, Row, Divider, Typography, Collapse } from "antd";
import {
  CalendarOutlined,
  ClockCircleOutlined,
  TagOutlined,
} from "@ant-design/icons";
import moment from "moment";
import ReportObject from "../../common/object/ReportObject";
import { ResultParam } from "../../common/object/ResultParam";
import { getColorForReportStatus, getIconForReport2Status, convertTimeToString } from "../../common/utilities/ReportUtilities";
import ResultInfoCard from "../common/result_info_card/ResultInfoCard";
import TestCaseReportContent2 from "./TestCaseReportContent2";
import SubResultsCard from "../subresults_card/SubResultCard";
import FailAnalyticTable, { FailAnalytic } from "../fail_analytic_table/FailAnalyticTable";

interface TestCaseReportProp {
  caseResult: ReportObject;
}

export default function TestCaseReport2(prop: TestCaseReportProp) {
  const startTime = new Date(prop.caseResult.meta.startTimeStamp as number);
  const start = moment(startTime);
  const startTimeStr = start.format("YYYY/MM/DD HH:mm:ss");

  const tcParams: ResultParam[] = prop.caseResult.meta.params ? (prop.caseResult.meta.params as ResultParam[]) : [];
  const tcOutputs: Record<string, string> = prop.caseResult.meta.tcOutput ? (prop.caseResult.meta.tcOutput as Record<string, string>) : {};
  const subResults: ReportObject[] = prop.caseResult.subResults ? prop.caseResult.subResults : [];
  const failAnalytic = prop.caseResult.meta.failAnalytic as FailAnalytic | undefined;

  return (
    <Row>
      <Col span={24}>
        <Row>
          <Col span={12} style={{ paddingRight: "10px" }}>
            <ResultInfoCard
              title="Test Case:"
              value={prop.caseResult.name}
              color="#003153"
              icon={<TagOutlined style={{ fontSize: 24 }} />}
            />
          </Col>
          <Col span={12} style={{ paddingLeft: "10px" }}>
            <ResultInfoCard
              title="Result:"
              value={prop.caseResult.status}
              color={getColorForReportStatus(prop.caseResult.status)}
              icon={getIconForReport2Status(prop.caseResult.status, 24)}
            />
          </Col>
        </Row>
        <Row style={{ marginTop: "20px" }}>
          <Col span={12} style={{ paddingRight: "10px" }}>
            <ResultInfoCard
              title="Started At:"
              value={startTimeStr}
              color="#dcc713"
              icon={<CalendarOutlined style={{ fontSize: 24 }} />}
            />
          </Col>
          <Col span={12} style={{ paddingLeft: "10px" }}>
            <ResultInfoCard
              title="Execution Time:"
              value={convertTimeToString(
                prop.caseResult.meta.execute_time as number
              )}
              color="#0fa9ea"
              icon={<ClockCircleOutlined style={{ fontSize: 24 }} />}
            />
          </Col>
        </Row>
        {failAnalytic ? (
          <Row>
            <Col span={24}>
              <Card className="report-info-card report-card-border">
                <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                  <Collapse.Panel className="report-content-collapse-header" header={<Divider className="report-content-divider-collapse">Fail Analytic</Divider>} key="1">
                    <FailAnalyticTable analytic={failAnalytic} />
                  </Collapse.Panel>
                </Collapse>
              </Card>
            </Col>
          </Row>
        ) : null}
        {tcParams.length > 0 ? (
          <Row>
            <Col span={24}>
              <Card className="report-info-card">
                <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                  <Collapse.Panel className="report-content-collapse-header" header={<Divider className="report-content-divider-collapse">Input Value</Divider>} key="1">
                    <Descriptions bordered className="report-input-desc" style={{ margin: 0 }} column={1}>
                      {tcParams.map((param: ResultParam, index: number) => (
                        <Descriptions.Item key={`${index + 1}`} label={param.paramName}>
                          <Typography.Paragraph ellipsis={{ rows: 3, expandable: true, symbol: 'See more' }} copyable={{ text: param.displayValue }}>
                            {param.displayValue}
                          </Typography.Paragraph>
                        </Descriptions.Item>
                      ))}
                    </Descriptions>
                  </Collapse.Panel>
                </Collapse>
              </Card>
            </Col>
          </Row>
        ) : null}
        {Object.entries(tcOutputs).length > 0 ? (
          <Row>
            <Col span={24}>
              <Card className="report-info-card">
                <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                  <Collapse.Panel className="report-content-collapse-header" header={<Divider className="report-content-divider-collapse">Output Value</Divider>} key="1">
                    <Descriptions bordered className="report-input-desc" style={{ margin: 0 }} column={1}>
                      {Object.entries(tcOutputs).map(([key, value]: string[], index: number) => (
                        <Descriptions.Item key={`${index + 1}`} label={key}>
                          <Typography.Paragraph ellipsis={{ rows: 3, expandable: true, symbol: 'See more' }} copyable={{ text: value }}>
                            {value}
                          </Typography.Paragraph>
                        </Descriptions.Item>
                      ))}
                    </Descriptions>
                  </Collapse.Panel>
                </Collapse>
              </Card>
            </Col>
          </Row>
        ) : null}
        <Row>
          <Col span={24}>
            <TestCaseReportContent2 caseResult={prop.caseResult} />
          </Col>
        </Row>
        {subResults.length > 0 ? (
          <Row>
            <Col span={24}>
              <SubResultsCard results={subResults} />
            </Col>
          </Row>
        ) : null}
      </Col>
    </Row>
  );
}
