import { Typography } from "antd";
import { PlatformObject } from "../../common/object/PlatformObject";
import ReportObject from "../../common/object/ReportObject";
import ReportType from "../../common/object/ReportType";
import { ResultParam } from "../../common/object/ResultParam";
import StepResultTableRow from "../../common/object/table_row_data/StepResultTableRow";
import { getIconForPlatform, getColorForReportStatus, getIconForReport2Status, convertTimeToString } from "../../common/utilities/ReportUtilities";

import ResultParamCell from "../step_result_table/ResultParamCell";
import { StepResultTableColumnName } from "../step_result_table/StepResultTableColumnName";

const { Paragraph, Text } = Typography;

export default class StepResultTableCellRenderSupport2 {
  col: StepResultTableColumnName;

  constructor(col: StepResultTableColumnName) {
    this.col = col;
  }

  getCellRender(row: StepResultTableRow, toggleTime:boolean) {
    const report = row.result as ReportObject;
    switch (this.col) {
      case StepResultTableColumnName.NAME: {
        let component: JSX.Element = (
          <Typography.Text>{`${row.key} ${row.result.name}`}</Typography.Text>
        );
        switch (row.result.typeName) {
          case ReportType.FORDATA:
          case ReportType.FORINDEX:
            component = (
              <Typography.Text style={{ color: "#2db7f5" }}>
                {`${row.key} ${row.result.name}`} (
                {report.results ? report.results.length : 0} row(s))
              </Typography.Text>
            );
            break;
          case ReportType.IF:
            component = (
              <Typography.Text
                style={{ color: "#d48806" }}
              >{`${row.key} ${row.result.name}`}</Typography.Text>
            );
            break;
          case ReportType.BREAK:
            component = (
              <Typography.Text
                style={{ color: "#cf1322" }}
              >{`${row.key} ${row.result.name}`}</Typography.Text>
            );
            break;
          case ReportType.CONTINUE:
            component = (
              <Typography.Text
                style={{ color: "#f50" }}
              >{`${row.key} ${row.result.name}`}</Typography.Text>
            );
            break;
          case ReportType.GROUP:
            component = (
              <Typography.Text
                style={{ color: "#87d068" }}
              >{`${row.key} ${row.result.name}`}</Typography.Text>
            );
            break;
          case ReportType.CUSTOMKEYWORD:
            component = (
              <Typography.Text
                style={{ color: "#108ee9" }}
              >{`${row.key} ${row.result.name}`}</Typography.Text>
            );
            break;
          default:
            break;
        }
        return component;
      }

      case StepResultTableColumnName.PLATFORM:
        return (
          <Paragraph
            style={{
              marginBottom: 0,
            }}
          >
            {getIconForPlatform(
              (report.meta.platform as PlatformObject).type,
              14
            )}
            <Text
              style={{
                marginLeft: "5px",
              }}
            >
              {(report.meta.platform as PlatformObject).displayName
                ? (report.meta.platform as PlatformObject).displayName
                : (report.meta.platform as PlatformObject).type}
            </Text>
          </Paragraph>
        );
      case StepResultTableColumnName.PARAMETER:
        return <ResultParamCell params={report.meta.params as ResultParam[]} />;
      case StepResultTableColumnName.STATUS:
        return (
          <Paragraph
            strong
            style={{
              marginBottom: 0,
              color: getColorForReportStatus(report.status),
            }}
          >
            {getIconForReport2Status(report.status, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color: getColorForReportStatus(report.status),
              }}
            >
              {row.result.status}
            </Text>
          </Paragraph>
        );
      case StepResultTableColumnName.TIME:
        return toggleTime ? `${report.meta.execute_time} ms` : convertTimeToString(report.meta.execute_time as number);
      default:
        return "";
    }
  }
}
