import React, { Key, useEffect, useState } from "react";
import ResizeObserver from 'rc-resize-observer';
import { Card, Col, Descriptions, Row, Typography, Tree } from "antd";
import UIObjectElementTreeNode from "../../../../../common/object/tree_data/UIObjectElementTreeNode";

const { Paragraph } = Typography;
const { DirectoryTree } = Tree;

interface UIObjectDetailProp {
  locatorListJson: string;
}

export default function UIObjectDetail(prop: UIObjectDetailProp) {
  const [elementTree, setElementTree] = useState<UIObjectElementTreeNode[]>([]);
  const [selectedKey, setSelectedKey] = useState<string[]>([]);
  const [locatorList, setLocatorList] = useState<any[] | undefined>();
  const [treeHeight, setTreeHeight] = useState<number>(0);

  let uiobj: any | undefined;
  try {
    uiobj = JSON.parse(prop.locatorListJson);
  } catch (e) {
    console.log(e);
  }

  useEffect(() => {
    if (uiobj) {
      const treeData = new UIObjectElementTreeNode(uiobj, false, `1`);
      setElementTree([treeData]);
      setSelectedKey([`1`]);
      setLocatorList(treeData.locatorList);
    }
  }, []);

  const findTreeNode = (key: string) => {
    let currentParent: UIObjectElementTreeNode[] | undefined = elementTree;
    let foundNode: UIObjectElementTreeNode | undefined;

    const splt = key.split('.');
    for (let i = 0; i < splt.length; i += 1) {
      const idx = Number.parseInt(splt[i], 10) - 1;
      if (i === splt.length - 1) {
        foundNode = currentParent ? currentParent[idx] : undefined;
      }
      currentParent = currentParent[idx].children;
      if (currentParent === undefined) break;
    }

    return foundNode;
  };

  const handleNodeSelect = (selectedKeys: Key[]) => {
    setSelectedKey(selectedKeys.map((key) => key.toString()));
    setLocatorList(findTreeNode(selectedKeys[0].toString())?.locatorList);
  };

  return uiobj ? (
    <Row className="uiobj-param-detail-wrapper">
      <Col span={7}>
        <Card title="Element Tree" className="uiobj-param-detail-card">
          <ResizeObserver
            onResize={(resize) => {
              setTreeHeight(resize.height);
            }}
          >
            <div style={{ height: '100%' }}>
              <DirectoryTree blockNode treeData={elementTree} selectedKeys={selectedKey} onSelect={handleNodeSelect} motion={null} className="projectTree" showIcon={false} height={treeHeight} />
            </div>
          </ResizeObserver>
        </Card>
      </Col>
      <Col span={1} />
      <Col span={16}>
        {locatorList !== undefined ? (
          <Card title="Element Info" className="uiobj-param-detail-card">
            <Descriptions column={1} bordered>
              {locatorList.map((locator, idx) => (
                // eslint-disable-next-line react/no-array-index-key
                <Descriptions.Item label={locator.strategy} key={idx}>
                  <Paragraph style={{ whiteSpace: 'pre-wrap', margin: 0 }} copyable ellipsis={{ expandable: true, symbol: 'See more', rows: 1 }} title={locator.value}>
                    {locator.value}
                  </Paragraph>
                </Descriptions.Item>
              ))}
            </Descriptions>
          </Card>
        ) : null}
      </Col>
    </Row>
  ) : null;
}
