import { Divider } from "antd";
import React from "react";
import ResultObject from "../../common/object/ResultObject";
import StepResultTable from "./StepResultTable";

interface StepResultWrapperProp {
  tcResult: ResultObject;
}

export default function StepResultWrapper(prop: StepResultWrapperProp) {
  return (
    <>
      {prop.tcResult.setUpResult.length > 0 && (
        <>
          <Divider>Set Up</Divider>
          <StepResultTable stepResultList={prop.tcResult.setUpResult} stepIdx="1"/>
        </>
      )}
      {prop.tcResult.results.length > 0 && (
        <>
          <Divider style={{ marginTop: "20px" }}>Main Execution</Divider>
          <StepResultTable stepResultList={prop.tcResult.results} stepIdx="2"/>
        </>
      )}
      {prop.tcResult.tearDownResult.length > 0 && (
        <>
          <Divider style={{ marginTop: "20px" }}>Tear Down</Divider>
          <StepResultTable stepResultList={prop.tcResult.tearDownResult} stepIdx="3"/>
        </>
      )}
    </>
  );
}
