import { PlatformObject } from "../PlatformObject";
import ReportObject from "../ReportObject";
import ReportStatus from "../ReportStatus";
import ResultObject from "../ResultObject";
import { ResultStatus } from "../ResultStatus";

export default class TestCaseRoundTableRow {
  key: number;
  roundName: string;
  status: ResultStatus | ReportStatus;
  platformName: string;
  executeTime: number;
  resultObj: ResultObject | ReportObject;

  constructor(index: number, caseResult: ResultObject | ReportObject) {
    this.key = index;
    this.roundName = caseResult.name;
    this.status = caseResult.status;
    this.resultObj = caseResult;

    if ((caseResult as ReportObject).meta !== undefined) {
      const report = caseResult as ReportObject;
      this.platformName = (report.meta.platform as PlatformObject).displayName;
      this.executeTime = report.meta.execute_time as number;
    } else {
      const result = caseResult as ResultObject;
      this.platformName = result.platform.displayName;
      this.executeTime = result.executeTime;
    }
  }
}
