import { Card, Collapse, Divider } from 'antd';
import SubResultsTable from './SubResultsTable';
import ReportObject from '../../common/object/ReportObject';

type SubResultCardProp = {
  results: ReportObject[];
};

export default function SubResultsCard(prop: SubResultCardProp) {
  return (
    <Card className="report-info-card report-card-border">
      <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
        <Collapse.Panel className="report-content-collapse-header" header={<Divider className="report-content-divider-collapse">Sub Step</Divider>} key="1">
          <SubResultsTable results={prop.results} />
        </Collapse.Panel>
      </Collapse>
    </Card>
  );
}
