import React from "react";
import { Typography } from 'antd';
import ResultTreeTitle from '../../../component/common/title/ResultTreeTitle';
import { getColorForReportStatus, getColorForStatus, getIconForReport2Status, getIconForReportStatus } from '../../utilities/ReportUtilities';
import ResultObject from '../ResultObject';
import { ResultType } from '../ResultType';
import ReportObject from "../ReportObject";
import ReportStatus from "../ReportStatus";
import ReportType from "../ReportType";

export default class ResultTreeNode {
  key!: string;
  title!: JSX.Element | null;
  icon!: JSX.Element | null;
  children?: ResultTreeNode[];
  selectable = true;
  searchTerm!: string;
  pathToResult!: number[];

  constructor(key: string, obj: ResultObject | ReportObject, pathToResult: number[]) {
    this.key = key;
    if ((obj as ReportObject).meta === undefined) {
      this.consumeResultObject(obj as ResultObject, pathToResult);
    } else {
      this.consumeReportObject(obj as ReportObject, pathToResult);
    }
  }

  consumeResultObject(obj: ResultObject, pathToResult: number[]) {
    this.searchTerm = obj.name;
    this.pathToResult = pathToResult;
    const generatedId = pathToResult.join('-');
    this.title = <ResultTreeTitle text={obj.name} resultId={generatedId} />;
    this.icon = (
      <Typography.Text title={obj.name} style={{ color: getColorForStatus(obj.status) }}>
        {getIconForReportStatus(obj.status, 14)}
      </Typography.Text>
    );
    if (obj.results.length > 0 && obj.typeName !== ResultType.TESTCASE) {
      this.children = obj.results.map((childRs, index) => new ResultTreeNode(`${this.key}.${index + 1}`, childRs, [...pathToResult, index]));
    }
    if (obj.typeName === ResultType.TESTCASESUITE) this.selectable = false;
  }

  consumeReportObject(obj: ReportObject, pathToResult: number[]) {
    this.searchTerm = obj.name;
    this.pathToResult = pathToResult;

    if (obj.typeName === ReportType.TESTSUITE && obj.results) {
      //  Create fake tc tree node to group round result
      const mapping: Map<string, { name: string; platform: Record<string, string>; status: ReportStatus; rounds: ReportObject[] }> = new Map();
      const roundIndexMapping: Map<ReportObject, number> = new Map();
      obj.results.forEach((childRs, index) => {
        roundIndexMapping.set(childRs, index);
        if (childRs.typeName === ReportType.NOTEXECUTE) return;

        const groupingId = childRs.meta.groupId as string;
        const currentObj = mapping.get(groupingId);
        if (currentObj) {
          currentObj.rounds.push(childRs);
          if (childRs.status === ReportStatus.PASS && currentObj.status === ReportStatus.NOTEXECUTE) currentObj.status = ReportStatus.PASS;
          else if (childRs.status === ReportStatus.FAIL && currentObj.status !== ReportStatus.STOP) currentObj.status = ReportStatus.FAIL;
          else if (childRs.status === ReportStatus.STOP) currentObj.status = ReportStatus.STOP;
        } else {
          mapping.set(groupingId, { name: childRs.meta.tcName as string, status: childRs.status, platform: childRs.meta.platform as Record<string, string>, rounds: [childRs] });
        }
      });

      let idx = 0;
      this.children = [];
      mapping.forEach((value, key) => {
        const fakeNode = new ResultTreeNode(`${this.key}.${idx + 1}`, { name: '', typeName: ReportType.NOTEXECUTE, status: value.status, meta: {} }, []);
        fakeNode.searchTerm = value.name;
        fakeNode.pathToResult = [-1];
        fakeNode.title = <ResultTreeTitle text={value.name} resultId={key} />;
        fakeNode.icon = (
          <Typography.Text title={value.name} style={{ color: getColorForReportStatus(value.status) }}>
            {getIconForReport2Status(value.status, 14)}
          </Typography.Text>
        );
        fakeNode.selectable = false;
        fakeNode.children = value.rounds.map((round, index) => {
          const trueRoundIndex = roundIndexMapping.get(round);
          return new ResultTreeNode(`${fakeNode.key}.${index + 1}`, round, [...pathToResult, trueRoundIndex !== undefined ? trueRoundIndex : -1]);
        });
        this.children?.push(fakeNode);

        idx += 1;
      });
    }

    switch (obj.typeName) {
      case ReportType.TESTCASE:
      case ReportType.TESTSUITE:
      case ReportType.TESTFLEX:
      case ReportType.JMTESTCASE:
      case ReportType.FLEX_TC:
      case ReportType.FLEX_TS:
        this.selectable = true;
        break;
      case ReportType.NOTEXECUTE:
        if (obj.status === ReportStatus.FAIL) this.selectable = true;
        else this.selectable = false;
        break;
      default:
        this.selectable = false;
        break;
    }


    const generatedId = pathToResult.join('-');
    this.title = <ResultTreeTitle text={obj.name} resultId={generatedId} />;
    this.icon = (
      <Typography.Text title={obj.name} style={{ color: getColorForReportStatus(obj.status) }}>
        {getIconForReport2Status(obj.status, 14)}
      </Typography.Text>
    );

    if (obj.results !== undefined && this.children === undefined && obj.typeName !== ReportType.TESTCASE && obj.typeName !== ReportType.JMTESTCASE && obj.typeName !== ReportType.FLEX_TC) {
      this.children = [];
      let treeKeyIndex = 0;
      obj.results.forEach((childRs, index) => {
        if (childRs.typeName === ReportType.NOTEXECUTE && childRs.status !== ReportStatus.FAIL) return;

        this.children?.push(new ResultTreeNode(`${this.key}.${treeKeyIndex + 1}`, childRs, [...pathToResult, index]));
        treeKeyIndex += 1;
      });
    }
    
  }
}
