import React from "react";
import { Typography } from "antd";

import { ResultParam } from "../../common/object/ResultParam";
import { store } from "../../common/store";
import { ReportParamDetailModalActionType } from "../../common/object/reducer/RerportParamDetailModalReducer";
import { hasCopyValue } from "../../common/utilities/ReportUtilities";

const { Paragraph } = Typography;

interface ResultParamCellProp {
  params?: ResultParam[];
}

export default function ResultParamCell(prop: ResultParamCellProp) {
  const generateParamValue = (param: ResultParam) => {
    switch (param.paramType) {
      case "UIObject":
      case "Web UIObject":
      case "Mobile UIObject":
      case "Windows UIObject":
      case "TestDataObject":
      case "Data Slice":
      case "WebServiceObject":
      case "DatabaseQueryObject":
      case "ResponseObject":
      case "ValueMap":
        return (
          // eslint-disable-next-line jsx-a11y/anchor-is-valid
          <a
            onClick={() => {
              store.dispatch({
                type: ReportParamDetailModalActionType.OPEN_MODAL,
                content: {
                  paramType: param.paramType,
                  paramValue: param.detailValue,
                },
              });
            }}
            style={{ color: '#eb2f96' }}
          >
            {param.displayValue}
          </a>
        );
      
      default:
        return <span style={{ color: '#1d39c4' }}>{param.displayValue}</span>;
    }
  };

  return (
    <>
      {prop.params
        ? prop.params.map((param) => (
            <Paragraph
              key={`${param.paramName}:${param.paramType}`}
              style={{ whiteSpace: "pre-wrap", marginBottom: 0 }}
              ellipsis={{ rows: 3, expandable: true, symbol: "See more" }}
              copyable={
                hasCopyValue(param)
                  ? {
                      text: param.displayValue,
                      tooltips: false,
                    }
                  : undefined
              }
            >
              {`${param.paramName}:  `}
              {generateParamValue(param)}
            </Paragraph>
          ))
        : null}
    </>
  );
}
