import React, { useEffect, useRef, useState } from 'react';
import { createPortal } from 'react-dom';

function ShadowContent(prop: { root: any; children: React.ReactNode }) {
  return createPortal(prop.children, prop.root);
}

export default function ShadowRoot(prop: { children: React.ReactNode }) {
  const ref = useRef<HTMLDivElement>(null);
  const [shadowRoot, setShadowRoot] = useState<ShadowRoot | undefined>();

  useEffect(() => {
    if (ref.current !== null && shadowRoot === undefined) {
      setShadowRoot(ref.current.attachShadow({ mode: 'open' }));
    }
  }, [shadowRoot]);

  return <div ref={ref}>{shadowRoot ? <ShadowContent root={shadowRoot}>{prop.children}</ShadowContent> : null}</div>;
}
