import { Platform } from "../../common/object/Platform";
import { PlatformObject } from "../../common/object/PlatformObject";
import ReportObject from "../../common/object/ReportObject";
import { ResultParam } from "../../common/object/ResultParam";
import JMeterSummaryReport from "../step_result_table/JMeterSummaryReport";
import StepResultWrapper2 from "../step_result_table2/StepResultWrapper2";

type TestCaseReportContentProp = {
  caseResult: ReportObject;
};

export default function TestCaseReportContent2(prop: TestCaseReportContentProp) {
  switch ((prop.caseResult.meta.platform as PlatformObject).type) {
    case Platform.JMETER_PLATFORM:
      return (
        <JMeterSummaryReport
          output={prop.caseResult.meta.outputResult as ResultParam | undefined}
          status={prop.caseResult.status}
          failureCause={prop.caseResult.meta.error ? (prop.caseResult.meta.error as string) : ''}
          name={prop.caseResult.name}
          jmeterReportId={prop.caseResult.meta.runId ? (prop.caseResult.meta.runId as string) : ''}
        />
      );
    default:
      return <StepResultWrapper2 tcResult={prop.caseResult}  />;
  }
}
