import { Descriptions, Typography } from 'antd';

export type FailAnalytic = { exceptionType: string; message: string; location: string };

type FailAnalyticTableProp = {
  analytic: FailAnalytic;
};

export default function FailAnalyticTable(prop: FailAnalyticTableProp) {
  return (
    <Descriptions column={1} bordered>
      <Descriptions.Item label="Type">
        <Typography.Text style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} type="danger" strong>
          {prop.analytic.exceptionType}
        </Typography.Text>
      </Descriptions.Item>
      <Descriptions.Item label="Location">
        <Typography.Text style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} strong>
          {prop.analytic.location}
        </Typography.Text>
      </Descriptions.Item>
      <Descriptions.Item label="Message">
        <Typography.Text style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }}>{prop.analytic.message}</Typography.Text>
      </Descriptions.Item>
    </Descriptions>
  );
}
