import React from "react";
import Icon, {
  CheckCircleOutlined,
  CloseCircleOutlined,
  StopOutlined,
  ForwardOutlined,
} from "@ant-design/icons";
import { imageRepo } from "../../component/icons/IconRepo";

import { ResultStatus } from "../object/ResultStatus";
import { ResultParam } from "../object/ResultParam";
import ReportStatus from "../object/ReportStatus";
import { roundTo } from "round-to";

export function getIconForReport2Status(status: ReportStatus, iconSize: number) {
  switch (status) {
    case ReportStatus.PASS:
      return <CheckCircleOutlined style={{ fontSize: iconSize }} />;
    case ReportStatus.FAIL:
      return <CloseCircleOutlined style={{ fontSize: iconSize }} />;
    case ReportStatus.STOP:
      return <StopOutlined style={{ fontSize: iconSize }} />;
    case ReportStatus.SKIP:
      return <ForwardOutlined style={{ fontSize: iconSize }} />;
    default:
      return null;
  }
}

export function getColorForReportStatus(status: ReportStatus) {
  switch (status) {
    case ReportStatus.PASS:
      return '#09af00';
    case ReportStatus.FAIL:
    case ReportStatus.STOP:
      return '#f01e23';
    case ReportStatus.SKIP:
      return '#fea101';
    default:
      return '#383838';
  }
}

export function getIconForReportStatus(status: ResultStatus, iconSize: number) {
  switch (status) {
    case ResultStatus.PASSED:
      return <CheckCircleOutlined style={{ fontSize: iconSize }} />;
    case ResultStatus.FAILED:
      return <CloseCircleOutlined style={{ fontSize: iconSize }} />;
    case ResultStatus.STOPPED:
      return <StopOutlined style={{ fontSize: iconSize }} />;
    case ResultStatus.SKIPPED:
      return <ForwardOutlined style={{ fontSize: iconSize }} />;
    default:
      return null;
  }
}

export function getColorForStatus(status: ResultStatus) {
  switch (status) {
    case ResultStatus.PASSED:
      return "#09af00";
    case ResultStatus.FAILED:
    case ResultStatus.STOPPED:
      return "#f01e23";
    case ResultStatus.SKIPPED:
      return "#fea101";
    default:
      return "#383838";
  }
}

export function convertTimeToString(milisec: number) {
  const sec = milisec / 1000;
  if (sec < 60) {
    return sec.toFixed(1) + " s";
  } else if (sec >= 60 && sec < 3600) {
    let minute = Math.floor(sec / 60);
    let remainSecond = sec - minute * 60;
    return minute + " min " + remainSecond.toFixed(1) + " s";
  } else {
    let hour = Math.floor(sec / 3600);
    let remainSecond = sec - hour * 3600;
    let minute = Math.floor(remainSecond / 60);
    let remainSecond2 = remainSecond - minute * 60;
    return hour + " h " + minute + " min " + remainSecond2.toFixed(1) + " s";
  }
}

