export function hasCopyValue(param: ResultParam) {
  switch (param.paramType) {
    case 'UIObject':
    case 'Web UIObject':
    case 'Mobile UIObject':
    case 'Windows UIObject':
    case 'TestDataObject':
    case 'Data Slice':
    case 'WebServiceObject':
    case 'DatabaseQueryObject':
    case 'ResponseObject':
    case 'ValueMap':
      return false;
    default:
      return true;
  }
}

export function roundNumber(value: number | string, precision: number, fractionDigit?: number) {
  if (typeof value === 'string') return value;
  if (fractionDigit !== undefined) return roundTo(value, precision).toFixed(fractionDigit);
  return roundTo(value, precision);
}

