import React from "react";
import { useSelector } from "react-redux";
import PathInfo from "../../common/object/PathInfo";
import { PathActionType } from "../../common/object/reducer/PathReducer";
import ResultObject from "../../common/object/ResultObject";
import { ResultType } from "../../common/object/ResultType";
import { getCurrentPath } from "../../common/object/selector/PathSelector";
import { store } from "../../common/store";
import ReportParamDetailModal from "../common/modal/report_param_detail_modal/ReportParamDetailModal";
import TestCaseReport from "../test_case_report/TestCaseReport";
import TestPlanReport from "../test_plan_report/TestPlanReport";
import TestSuiteReport from "../test_suite_report/TestSuiteReport";

declare const report: ResultObject;

export default function ReportFrame() {
  const currentPath = useSelector(getCurrentPath);
  let children = null;
  if (currentPath) {
    let currentResult = report;
    (currentPath as PathInfo).pathDirection.forEach((idx) => {
      currentResult = currentResult.results[idx];
    });
    if (currentResult !== undefined) {
      switch (currentResult.typeName) {
        case ResultType.TESTPLAN:
          children = <TestPlanReport planResult={currentResult} pathToResult={currentPath.pathDirection} />;
          break;
        case ResultType.TESTSUITE:
          children = <TestSuiteReport suiteResult={currentResult} pathToResult={currentPath.pathDirection} />;
          break;
        case ResultType.TESTCASE:
          children = <TestCaseReport caseResult={currentResult} />;
          break;
        default:
          break;
      }
    }
  }

  const handleKeyPress = (e: any) => {
    if (e.key === "Backspace") {
      store.dispatch({
        type: PathActionType.GO_BACK,
        content: undefined,
      });
    }
  };

  return (
    <div tabIndex={-1} onKeyDown={handleKeyPress}>
      {children}
      <ReportParamDetailModal />
    </div>
  );
}
