import { Card, Divider, Table, Typography } from 'antd';
import React, { useMemo } from 'react';
import ReportStatus from '../../common/object/ReportStatus';
import { ResultParam } from '../../common/object/ResultParam';
import { ResultStatus } from '../../common/object/ResultStatus';
import { roundNumber } from '../../common/utilities/ReportUtilities';

const { Paragraph } = Typography;

type JMeterSummaryReportProp = {
  output?: ResultParam;
  status: ResultStatus | ReportStatus;
  failureCause: string;
  name: string;
  jmeterReportId: string;
};

export default function JMeterSummaryReport(prop: JMeterSummaryReportProp) {
  const columns = [
    {
      title: 'Requests',
      children: [{ title: 'Label', dataIndex: 'transaction', key: 'transaction' }],
    },
    {
      title: 'Executions',
      children: [
        { title: '#Samples', dataIndex: 'sampleCount', key: 'sampleCount' },
        { title: 'FAIL', dataIndex: 'errorCount', key: 'errorCount' },
        { title: 'Error %', dataIndex: 'errorPct', key: 'errorPct', render: (num: number) => `${roundNumber(num, 2, 2)} %` },
      ],
    },
    {
      title: 'Response Times (ms)',
      children: [
        { title: 'Average', dataIndex: 'meanResTime', key: 'meanResTime', render: (num: number) => roundNumber(num, 2, 2) },
        { title: 'Min', dataIndex: 'minResTime', key: 'minResTime', render: (num: number) => roundNumber(num, 0) },
        { title: 'Max', dataIndex: 'maxResTime', key: 'maxResTime', render: (num: number) => roundNumber(num, 0) },
        { title: 'Median', dataIndex: 'medianResTime', key: 'medianResTime', render: (num: number) => roundNumber(num, 2, 2) },
        { title: '90th pct', dataIndex: 'pct1ResTime', key: 'pct1ResTime', render: (num: number) => roundNumber(num, 2, 2) },
        { title: '95th pct', dataIndex: 'pct2ResTime', key: 'pct2ResTime', render: (num: number) => roundNumber(num, 2, 2) },
        { title: '99th pct', dataIndex: 'pct3ResTime', key: 'pct3ResTime', render: (num: number) => roundNumber(num, 2, 2) },
      ],
    },
    {
      title: 'Throughput',
      children: [{ title: 'Transactions/s', dataIndex: 'throughput', key: 'throughput', render: (num: number) => roundNumber(num, 2, 2) }],
    },
    {
      title: 'Network (KB/sec)',
      children: [
        { title: 'Received', dataIndex: 'receivedKBytesPerSec', key: 'receivedKBytesPerSec', render: (num: number) => roundNumber(num, 2, 2) },
        { title: 'Sent', dataIndex: 'sentKBytesPerSec', key: 'sentKBytesPerSec', render: (num: number) => roundNumber(num, 2, 2) },
      ],
    },
  ];

  const tableData = useMemo(() => {
    const rs: any[] = [];
    if (prop.output) {
      const obj = JSON.parse(prop.output?.displayValue);
      Object.keys(obj).forEach((key, index) => {
        rs.push({ key: index, ...obj[key] });
      });
    }
    return rs;
  }, [prop.output]);

  if (prop.status === ResultStatus.FAILED || prop.status === ReportStatus.FAIL) {
    return (
      <Card className="report-info-card report-card-border">
        <Divider>Error Log</Divider>
        <Paragraph code style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} copyable>
          {prop.failureCause}
        </Paragraph>
      </Card>
    );
  }

  return <Table className="custom-table report-table" columns={columns} dataSource={tableData} bordered />;
}
