import { combineReducers, createStore } from "@reduxjs/toolkit";
import { pathReducer } from "./object/reducer/PathReducer";
import { reportParamDetailModalReducer } from "./object/reducer/RerportParamDetailModalReducer";

const combineReducer = combineReducers({
  pathStore: pathReducer,
  reportParamDetailModalStore: reportParamDetailModalReducer,
});

export const store = createStore(combineReducer);
