import React, { useMemo } from "react";
import { Table, Typography } from "antd";
import { Platform } from "../../common/object/Platform";

import ResultObject from "../../common/object/ResultObject";
import { ResultStatus } from "../../common/object/ResultStatus";
import {
  convertTimeToString,
  getColorForStatus,
  getIconForPlatform,
  getIconForReportStatus,
} from "../../common/utilities/ReportUtilities";
import SummaryTestCaseTableRow from "../../common/object/table_row_data/SummaryTestCaseTableRow";
import TestCaseRoundComponent from "./TestCaseRoundComponent";
import TableRow from "../step_result_table/TableRow";

const { Text, Paragraph } = Typography;

interface TestCaseSummaryTableProp {
  suiteResult: ResultObject;
  pathToResult: number[];
}

export default function TestCaseSummaryTable(prop: TestCaseSummaryTableProp) {
  const [data, nameFilter, statusFilter, platformFilter] = useMemo(() => {
    const data: SummaryTestCaseTableRow[] = [];
    const nameFilter: any[] = [];
    const nameSet = new Set<string>();
    const statusSet = new Set<ResultStatus>();
    const statusFilter: any[] = [];
    const platformSet = new Set<string>();
    const platformFilter: any[] = [];
    prop.suiteResult.results.forEach((caseRs, index) => {
      data.push(new SummaryTestCaseTableRow(index + 1, caseRs));
      nameSet.add(caseRs.name);
      statusSet.add(caseRs.status);
      platformSet.add(caseRs.platform.displayName);
    });
    nameSet.forEach((name) => {
      nameFilter.push({ text: name, value: name });
    });
    statusSet.forEach((status) => {
      statusFilter.push({ text: status, value: status });
    });
    platformSet.forEach((platform) => {
      platformFilter.push({ text: platform, value: platform });
    });
    return [data, nameFilter, statusFilter, platformFilter];
  }, [prop.suiteResult.results]);

  const columns = [
    {
      title: "#",
      dataIndex: "key",
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.key - b.key,
    },
    {
      title: "Test Case",
      dataIndex: "caseName",
      filters: nameFilter,
      onFilter: (value: any, record: SummaryTestCaseTableRow) =>
        record.caseName === value,
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.caseName.localeCompare(b.caseName),
        render: (text: string, row: any) => <div>{text}</div>,
    },
    {
      title: "Platform",
      dataIndex: "platformName",
      filters: platformFilter,
      onFilter: (value: any, record: SummaryTestCaseTableRow) =>
        record.platformName === value,
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.platformName.localeCompare(b.status),
      render: (text: string, row: SummaryTestCaseTableRow) => {
        let color:string;
        switch ((row.resultObj as ResultObject).platform.type) {
          case Platform.WEB_PLATFORM:
          case Platform.CHROME_PLATFORM:
          case Platform.FIREFOX_PLATFORM:
          case Platform.SAFARI_PLATFORM:
          case Platform.EDGE_PLATFORM:
          case Platform.IE_PLATFORM:
            color = '#722ed1';
            break;
          case Platform.MOBILE_PLATFORM:
          case Platform.IOS_PLATFORM:
          case Platform.ANDROID_PLATFORM:
            color = '#5EB0E5';
            break;
          case Platform.WINDOWS_PLATFORM:
            color = '#008080';
            break;
          default:
            color = 'rgba(0, 0, 0, 0.85)';
            break;
        }
        return (
          <Paragraph
            style={{
              marginBottom: 0,
            }}>
            {getIconForPlatform((row.resultObj as ResultObject).platform.type, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color
              }}>
              {text}
            </Text>
          </Paragraph>
        );
      },
    },
    {
      title: "Status",
      dataIndex: "status",
      filters: statusFilter,
      onFilter: (value: any, record: SummaryTestCaseTableRow) =>
        record.status === value,
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.status.localeCompare(b.status),
      render: (text: string, row: SummaryTestCaseTableRow) => {
        return (
          <Paragraph
            strong
            style={{
              marginBottom: 0,
              color: getColorForStatus(row.status as ResultStatus),
            }}>
            {getIconForReportStatus(row.status as ResultStatus, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color: getColorForStatus(row.status as ResultStatus),
              }}>
              {text}
            </Text>
          </Paragraph>
        );
      },
    },
    {
      title: "Execution Time",
      dataIndex: "executeTime",
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.executeTime - b.executeTime,
      render: (text: string, row: SummaryTestCaseTableRow) => {
        return convertTimeToString(row.executeTime);
      },
    },
  ];

  const components = {
    body:{
      row: TableRow,
    }
  };

  return (
    <Table
      columns={columns}
      dataSource={data}
      pagination={{ hideOnSinglePage: true }}
      expandable={{
        expandedRowRender: (record) => <TestCaseRoundComponent row={record} tcsIndex={record.key - 1} pathToResult={prop.pathToResult} />,
      }}
      components={components}
      className="report-table"
    />
  );
}
