import { Card, Divider, Typography } from 'antd';
import ReportObject from '../../common/object/ReportObject';
import ReportStatus from '../../common/object/ReportStatus';

type NotExecuteReportProp = {
  result: ReportObject;
};

export default function NotExecuteReport(prop: NotExecuteReportProp) {
  if (prop.result.status !== ReportStatus.FAIL) return null;
  return (
    <Card style={{ marginTop: '20px', width: '100%' }}>
      <Divider>Error Log</Divider>
      <Typography.Paragraph code copyable>
        {prop.result.meta.error as string}
      </Typography.Paragraph>
    </Card>
  );
}
