import React from "react";
import { Button, Modal } from "antd";

import { useSelector } from "react-redux";
import { getParamDetail } from "../../../../common/object/selector/ReportParamDetailModalSelector";
import { store } from "../../../../common/store";
import { ReportParamDetailModalActionType } from "../../../../common/object/reducer/RerportParamDetailModalReducer";
import UIObjectDetail from "./modal_body/UIObjectDetail";
import DataTableDetail from "./modal_body/DataTableDetail";
import DataSliceDetail from "./modal_body/DataSliceDetail";
import RequestObjectDetail from "./modal_body/RequestObjectDetail";
import DatabaseQueryDetail from "./modal_body/DatabaseQueryDetail";
import ResponseObjectDetail from "./modal_body/ResponseObjectDetail";
import ValueMapDetail from "./modal_body/ValueMapDetail";

export default function ReportParamDetailModal() {
  const paramDetail = useSelector(getParamDetail);

  const onCancel = () => {
    store.dispatch({
      type: ReportParamDetailModalActionType.CLOSE_MODAL,
      content: undefined,
    });
  };

  let modalBody = null;
  switch (paramDetail.paramType) {
    case "UIObject":
    case "Web UIObject":
    case "Mobile UIObject":
    case "Windows UIObject":
      modalBody = (
        <UIObjectDetail locatorListJson={paramDetail.paramStringValue} />
      );
      break;
    case "TestDataObject":
      modalBody = <DataTableDetail tableJson={paramDetail.paramStringValue} />;
      break;
    case "Data Slice":
      modalBody = <DataSliceDetail sliceJson={paramDetail.paramStringValue} />;
      break;
    case "WebServiceObject":
      modalBody = <RequestObjectDetail objJson={paramDetail.paramStringValue} />;
      break;
    case "DatabaseQueryObject":
      modalBody = <DatabaseQueryDetail objJson={paramDetail.paramStringValue} />;
      break;
    case "ResponseObject":
      modalBody = <ResponseObjectDetail objJson={paramDetail.paramStringValue} />;
      break;
    case "ValueMap":
      modalBody = <ValueMapDetail valueMapJson={paramDetail.paramStringValue}/>;
      break;
    default:
      break;
  }
  const modalBodyWrapper = <div className="report-result-detail-modal-body">{modalBody}</div>;

  const footer = [<Button onClick={onCancel}>Return</Button>];

  return paramDetail.visible ? (
    <Modal
      centered
      width="80%"
      visible
      footer={footer}
      onCancel={onCancel}
      title={paramDetail.paramType}
    >
      {modalBodyWrapper}
    </Modal>
  ) : null;
}
