import React from "react";
import { Resizable } from "react-resizable";

export default function TableHeader({ onResize, width, ...restProps }: any) {
  if (!width) {
    return <th {...restProps} />;
  }

  return (
    <Resizable
      width={width}
      height={0}
      onResize={onResize}
      draggableOpts={{ enableUserSelectHack: false }}
    >
      <th {...restProps} />
    </Resizable>
  );
}
