import { Row, Table, Select, Form, Input, Button, Col, Divider } from "antd";
import { useForm } from "antd/lib/form/Form";
import { useEffect, useState } from "react";
import ResultObject from "../../common/object/ResultObject";
import { ResultStatus } from "../../common/object/ResultStatus";
import SummaryTestCaseTableRow from "../../common/object/table_row_data/SummaryTestCaseTableRow";
import TestCaseRoundTableRow from "../../common/object/table_row_data/TestCaseRoundTableRow";
import {
  convertTimeToString,
  getColorForStatus,
  getIconForReportStatus,
} from "../../common/utilities/ReportUtilities";
import TableCellTestNameTitle from "../common/title/TableCellTestNameTitle";
import StepResultComponent from "./StepResultComponent";

const { Option } = Select;

interface TestCaseRoundComponentProp {
  row: SummaryTestCaseTableRow;
  tcsIndex: number;
  pathToResult: number[];
}

export default function TestCaseRoundComponent(prop: TestCaseRoundComponentProp) {
  const [form] = useForm();
  const [dataTable, setDataTable] = useState<TestCaseRoundTableRow[]>([]);
  const [filteredDataTable, setFilteredDataTable] = useState<TestCaseRoundTableRow[]>([]);
  const [statusFilter, setStatusFilter] = useState<{text: string, value: ResultStatus}[]>([]);
  
  useEffect(() => {
    const rows: TestCaseRoundTableRow[] = [];
    const statusFilterArr: any[] = [];
    const statusSet = new Set<ResultStatus>();

    (prop.row.resultObj as ResultObject).results.forEach(
      (val, index) => {
        rows.push(new TestCaseRoundTableRow(index + 1, val));
        statusSet.add(val.status);
      }
    );
    statusSet.forEach((status) => {
      statusFilterArr.push({ text: status, value: status });
    });

    setDataTable(rows);
    setStatusFilter(statusFilterArr);
    setFilteredDataTable(rows);
  }, [prop.row]);
  
  const columns = [
    {
      title: "Round",
      key: "round",
      render: (text: string, row: TestCaseRoundTableRow) => {
        return <TableCellTestNameTitle indexToPath={[...prop.pathToResult, prop.tcsIndex, row.key - 1]} resultObject={row.resultObj} text={row.roundName} preferDisplayName={`${prop.row.caseName} (${row.roundName})`} />;
      },
    },
    {
      title: "Status",
      key: "status",
      onFilter: (value: any, record: TestCaseRoundTableRow) => record.status === value,
      filters: statusFilter,
      render: (text: string, row: TestCaseRoundTableRow) => (
        <div>
          <span
            style={{
              marginBottom: 0,
              color: getColorForStatus(row.status  as ResultStatus),
            }}>
            {" "}
            {getIconForReportStatus(row.status  as ResultStatus, 14)}
          </span>
          <span
            style={{
              marginLeft: "5px",
              color: getColorForStatus(row.status  as ResultStatus),
            }}>
            {row.status}
          </span>
        </div>
      ),
    },
    {
      title: "Execute Time",
      key: "executeTime",
      render: (text: string, row: any) => {
        return <div>{convertTimeToString(row.executeTime)}</div>;
      },
    },
  ];

  const handleSubmitForm = (value: any) => {
    if (value.select !== "all") {
      const newDataTable = dataTable.filter((res) => {
        const newArr = (res.resultObj as ResultObject).parameter.find((param) => {
          if (value.value === "") {
            return param.paramName === value.select;
          } else {
            return (
              param.paramName === value.select &&
              param.displayValue === value.value
            );
          }
        });
        return newArr;
      });
      setFilteredDataTable(newDataTable);
    } else {
      setFilteredDataTable(dataTable);
    }
  };

  const handleChangeSelect = () => {
    form.setFieldsValue({ value: "" });
  };

  const paramNameArr = (prop.row.resultObj as ResultObject).results
    .map((result) => result.parameter.map((param) => param.paramName))
    .flat()
    .filter((val, index, arr) => arr.indexOf(val) === index);

  return (
    <>
      <Row>
        <Divider>Input Value Filter</Divider>
        <Form
          form={form}
          layout="inline"
          onFinish={handleSubmitForm}
          style={{ width: "100%", paddingBottom: "10px" }}>
          <Col span={10}>
            <Form.Item name="select" initialValue="all">
              <Select onChange={handleChangeSelect}>
                <Option value="all">Get All</Option>
                {paramNameArr.map((name) => (
                  <Option key={name} value={name}>
                    {name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={10}>
            <Form.Item name="value" initialValue="">
              <Input placeholder="Input Value" />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item>
              <Button
                htmlType="submit"
                type="primary"
                style={{ width: "100%" }}>
                Filter
              </Button>
            </Form.Item>
          </Col>
        </Form>
      </Row>
      <Row>
        <Divider>Round Table</Divider>
        <Table
          style={{ width: "100%" }}
          columns={columns}
          expandable={{
            expandedRowRender: (record) => (
              <StepResultComponent params={(record.resultObj as ResultObject).parameter} />
            ),
          }}
          dataSource={filteredDataTable}
          pagination={{ hideOnSinglePage: true }}
        />
      </Row>
    </>
  );
}
