import ResultObject from "../ResultObject";
import { ResultStatus } from "../ResultStatus";

export default class SummarySuiteTableRow {
  key: number;
  suiteName: string;
  status: ResultStatus;
  tcPass: number = 0;
  tcFail: number = 0;
  tcStop: number = 0;
  executeTime: number;
  resultObj: ResultObject;

  constructor(index: number, suiteResult: ResultObject) {
    this.key = index;
    this.suiteName = suiteResult.name;
    this.status = suiteResult.status;
    this.resultObj = suiteResult;
    this.executeTime = suiteResult.executeTime;
    suiteResult.results.forEach((tcResult) => {
      switch (tcResult.status) {
        case ResultStatus.PASSED:
          this.tcPass += 1;
          break;
        case ResultStatus.FAILED:
          this.tcFail += 1;
          break;
        case ResultStatus.STOPPED:
          this.tcStop += 1;
          break;
      }
    });
  }
}
