import "./App.scss";
import ResultObject from "./common/object/ResultObject";
import ReportObject from "./common/object/ReportObject";
import ReportViewer from "./component/report_frame/ReportViewer";
import ReportViewer2 from "./component/report_frame2/ReportViewer2";

declare const report: ResultObject | ReportObject;

function App() {
  const isReport2 = (report as ReportObject).meta !== undefined;
  return isReport2 ? <ReportViewer2 /> : <ReportViewer />
}

export default App;
