import { Descriptions } from "antd";
import React from "react";
import { ResultParam } from "../../common/object/ResultParam";

interface StepResultComponentProp {
  params: ResultParam[];
}

export default function StepResultComponent(prop: StepResultComponentProp) {
  return (
    <Descriptions title="Input Value" bordered style={{ margin: 0 }} column={1}>
      {prop.params.map((param: any, index: number) => (
        <Descriptions.Item
          key={`${index + 1}`}
          label={param.paramName}>
          {param.displayValue}
        </Descriptions.Item>
      ))}
    </Descriptions>
  );
}
