import { Typography } from 'antd';
import { PlatformObject } from '../../common/object/PlatformObject';
import ReportObject from '../../common/object/ReportObject';
import ReportStatus from '../../common/object/ReportStatus';
import ReportType from '../../common/object/ReportType';
import { ResultParam } from '../../common/object/ResultParam';
import StepResultTableRow from '../../common/object/table_row_data/StepResultTableRow';
import { convertTimeToString, getColorForReportStatus, getIconForPlatform, getIconForReport2Status } from '../../common/utilities/ReportUtilities';
import TableCellTestNameTitle from '../common/title/TableCellTestNameTitle';
import ResultParamCell from '../step_result_table/ResultParamCell';

export default class TestFlexTableCellRenderSupport2 {
  col: string;

  constructor(col: string) {
    this.col = col;
  }

  getCellRender(row: StepResultTableRow) {
    const rs = row.result as ReportObject;
    switch (this.col) {
      case 'Name': {
        let component: JSX.Element = <Typography.Text>{`${row.key} ${row.result.name}`}</Typography.Text>;
        switch (row.result.typeName) {
          case ReportType.FORDATA:
          case ReportType.FORINDEX:
            component = (
              <Typography.Text style={{ color: '#d48806' }}>
                {`${row.key} ${row.result.name}`} ({rs.results ? rs.results.length : 0} row(s))
              </Typography.Text>
            );
            break;
          case ReportType.IF:
          case ReportType.BREAK:
          case ReportType.CONTINUE:
          case ReportType.IF_PASS:
          case ReportType.IF_PASS_SECTION:
            component = <Typography.Text style={{ color: '#d48806' }}>{`${row.key} ${row.result.name}`}</Typography.Text>;
            break;
          case ReportType.NOTEXECUTE:
            if (row.result.status !== ReportStatus.FAIL) break;
            else {
                const splt = row.key.split('.');
                const idxToPath: number[] = [];
                splt.forEach((idx) => {
                  idxToPath.push(Number.parseInt(idx, 10) - 1);
                });
                component = <TableCellTestNameTitle  indexToPath={idxToPath} resultObject={row.result} text={`${row.key} ${row.result.name}`} />;
                break;  
            }
          // eslint-disable-next-line no-fallthrough
          case ReportType.TESTCASE:
          case ReportType.FLEX_TC:
          case ReportType.TESTSUITE:
          case ReportType.FLEX_TS: {
            const splt = row.key.split('.');
            const idxToPath: number[] = [];
            splt.forEach((idx) => {
              idxToPath.push(Number.parseInt(idx, 10) - 1);
            });
            component = <TableCellTestNameTitle  indexToPath={idxToPath} resultObject={row.result} text={`${row.key} ${row.result.name}`} />;
            break;
          }
          default:
            break;
        }
        return component;
      }
      case 'Platform': {
        const po = rs.meta.platform as PlatformObject | undefined;
        if (po) {
          return (
            <Typography.Paragraph
              style={{
                marginBottom: 0,
              }}
            >
              {getIconForPlatform(po.type, 24)}
              <Typography.Text
                style={{
                  marginLeft: '5px',
                }}
              >
                {po.displayName ? po.displayName : po.type}
              </Typography.Text>
            </Typography.Paragraph>
          );
        }
        return null;
      }
      case 'Input Data': {
        return <ResultParamCell params={rs.meta.params as ResultParam[]} />;
      }
      case 'Status':
        return (
          <Typography.Paragraph
            strong
            style={{
              marginBottom: 0,
              color: getColorForReportStatus(rs.status),
            }}
          >
            {getIconForReport2Status(rs.status, 14)}
            <Typography.Text
              style={{
                marginLeft: '5px',
                color: getColorForReportStatus(rs.status),
              }}
            >
              {row.result.status}
            </Typography.Text>
          </Typography.Paragraph>
        );
      case 'Execute Time':
        return convertTimeToString(rs.meta.execute_time as number);
      default:
        return null;
    }
  }
}
