import React from "react";
import { Card, Col, Row, Typography } from "antd";

const { Title } = Typography;

interface ResultInfoCardProp {
  title: string;
  value: string;
  color: string;
  icon: JSX.Element | null;
}

export default function ResultInfoCard(prop: ResultInfoCardProp) {
  return (
    <Card className="result-info-card report-card-border">
      <div>
        <div
          className="result-info-icon"
          style={{ backgroundColor: prop.color, color: "white" }}
        >
          {prop.icon}
        </div>
        <div className="result-info-value">
          <Row>
            <Title level={4} style={{ margin: 0, color: prop.color }} ellipsis>
              {prop.title}
            </Title>
          </Row>
          <Row>
            <Title level={4} style={{ margin: 0, color: prop.color }} ellipsis>
              {prop.value}
            </Title>
          </Row>
        </div>
      </div>
    </Card>
  );
}
