import React, { useMemo } from "react";
import { Table, Typography } from "antd";

import ResultObject from "../../common/object/ResultObject";
import { ResultStatus } from "../../common/object/ResultStatus";
import SummarySuiteTableRow from "../../common/object/table_row_data/SummarySuiteTableRow";
import {
  convertTimeToString,
  getColorForStatus,
  getIconForReportStatus,
} from "../../common/utilities/ReportUtilities";
import TableCellTestNameTitle from "../common/title/TableCellTestNameTitle";
import TableRow from "../step_result_table/TableRow";

const { Text, Paragraph } = Typography;

interface TestSuiteSummaryTableProp {
  planResult: ResultObject;
  pathToResult: number[];
}

export default function TestSuiteSummaryTable(prop: TestSuiteSummaryTableProp) {
  const [data, nameFilter, statusFilter] = useMemo(() => {
    const data: SummarySuiteTableRow[] = [];
    const nameFilter: any[] = [];
    const nameSet = new Set<string>();
    const statusSet = new Set<ResultStatus>();
    const statusFilter: any[] = [];
    prop.planResult.results.forEach((suiteRs, index) => {
      data.push(new SummarySuiteTableRow(index + 1, suiteRs));
      nameSet.add(suiteRs.name);
      statusSet.add(suiteRs.status);
    });
    nameSet.forEach((name) => {
      nameFilter.push({ text: name, value: name });
    });
    statusSet.forEach((status) => {
      statusFilter.push({ text: status, value: status });
    });
    return [data, nameFilter, statusFilter];
  }, [prop.planResult.results]);

  const columns = [
    {
      title: "#",
      dataIndex: "key",
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.key - b.key,
    },
    {
      title: "Test Suite",
      dataIndex: "suiteName",
      filters: nameFilter,
      onFilter: (value: any, record: SummarySuiteTableRow) =>
        record.suiteName === value,
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.suiteName.localeCompare(b.suiteName),
      render: (text: string, row: SummarySuiteTableRow) => {
        return <TableCellTestNameTitle  indexToPath={[...prop.pathToResult, row.key - 1]} resultObject={row.resultObj} text={text} />;
      },
    },
    {
      title: "Status",
      dataIndex: "status",
      filters: statusFilter,
      onFilter: (value: any, record: SummarySuiteTableRow) =>
        record.status === value,
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.status.localeCompare(b.status),
      render: (text: string, row: SummarySuiteTableRow) => {
        return (
          <Paragraph
            strong
            style={{
              marginBottom: 0,
              color: getColorForStatus(row.status),
            }}>
            {getIconForReportStatus(row.status, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color: getColorForStatus(row.status),
              }}>
              {text}
            </Text>
          </Paragraph>
        );
      },
    },
    {
      title: "Execution Time",
      dataIndex: "executeTime",
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.executeTime - b.executeTime,
      render: (text: string, row: SummarySuiteTableRow) => {
        return convertTimeToString(row.executeTime);
      },
    },
    {
      title: "Pass TC",
      dataIndex: "tcPass",
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.tcPass - b.tcPass,
      render: (text: string, row: SummarySuiteTableRow) => {
        return <Text style={{ color: "#09af00" }}>{text}</Text>;
      },
    },
    {
      title: "Fail TC",
      dataIndex: "tcFail",
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.tcFail - b.tcFail,
      render: (text: string, row: SummarySuiteTableRow) => {
        return <Text style={{ color: "#f01e23" }}>{text}</Text>;
      },
    },
    {
      title: "Stop TC",
      dataIndex: "tcStop",
      sorter: (a: SummarySuiteTableRow, b: SummarySuiteTableRow) =>
        a.tcStop - b.tcStop,
      render: (text: string, row: SummarySuiteTableRow) => {
        return <Text style={{ color: "#fea101" }}>{text}</Text>;
      },
    },
  ];

  const components = {
    body:{
      row: TableRow,
    }
  };

  return (
    <Table
      columns={columns}
      dataSource={data}
      pagination={{ hideOnSinglePage: true }}
      className="report-table"
      components={components}
    />
  );
}
