import { Card, Col, Row } from 'antd';
import { TagOutlined, CalendarOutlined, ClockCircleOutlined } from '@ant-design/icons';
import moment from 'moment';
import ResultInfoCard from '../common/result_info_card/ResultInfoCard';
import { convertTimeToString, getColorForReportStatus, getIconForReport2Status } from '../../common/utilities/ReportUtilities';
import TestFlexReportTable2 from '../test_flex_table2/TestFlexReportTable2';
import ReportObject from '../../common/object/ReportObject';
import SubResultsCard from '../subresults_card/SubResultCard';

type TestFlexReport2Prop = {
  flexResult: ReportObject;
};

export default function TestFlexReport2(prop: TestFlexReport2Prop) {
  const startTime = new Date(prop.flexResult.meta.startTimeStamp as number);
  const start = moment(startTime);
  const startTimeStr = start.format('YYYY/MM/DD HH:mm:ss');

  const subResults: ReportObject[] = prop.flexResult.subResults ? prop.flexResult.subResults : [];

  return (
    <Row>
      <Col span={24}>
        <Row>
          <Col span={12} style={{ paddingRight: '10px' }}>
            <ResultInfoCard title="Test Flex:" value={prop.flexResult.name} color="#003153" icon={<TagOutlined style={{ fontSize: 24 }} />} />
          </Col>
          <Col span={12} style={{ paddingLeft: '10px' }}>
            <ResultInfoCard title="Result:" value={prop.flexResult.status} color={getColorForReportStatus(prop.flexResult.status)} icon={getIconForReport2Status(prop.flexResult.status, 24)} />
          </Col>
        </Row>
        <Row style={{ marginTop: '20px' }}>
          <Col span={12} style={{ paddingRight: '10px' }}>
            <ResultInfoCard title="Started At:" value={startTimeStr} color="#dcc713" icon={<CalendarOutlined style={{ fontSize: 24 }} />} />
          </Col>
          <Col span={12} style={{ paddingLeft: '10px' }}>
            <ResultInfoCard title="Execution Time:" value={convertTimeToString(prop.flexResult.meta.execute_time as number)} color="#0fa9ea" icon={<ClockCircleOutlined style={{ fontSize: 24 }} />} />
          </Col>
        </Row>
        {prop.flexResult.results && prop.flexResult.results.length > 0 ? (
          <Row style={{ marginTop: '20px' }}>
            <Col span={24}>
              <Card className="report-info-card report-card-border">
                <TestFlexReportTable2 resultList={prop.flexResult.results} stepIdx="" />
              </Card>
            </Col>
          </Row>
        ) : null}
        {subResults.length > 0 ? (
          <Row>
            <Col span={24}>
              <SubResultsCard results={subResults} />
            </Col>
          </Row>
        ) : null}
      </Col>
    </Row>
  );
}
