import RootStore from "../store/RootStore";

export const getPathList = (state: RootStore) => {
  return state.pathStore.pathInfo;
};

export const getCurrentPath = (state: RootStore) => {
  if (state.pathStore.pathInfo.length === 0) return undefined;
  return state.pathStore.pathInfo[state.pathStore.pathInfo.length - 1];
};

export const checkResultVisited = (resultId: string) => (state: RootStore) => {
  const visitedList = state.pathStore.visitedRs;
  return visitedList.includes(resultId);
};