import { RightOutlined, LeftOutlined } from "@ant-design/icons";
import { Layout, Row, Col, Button } from "antd";
import ResizeObserver from "rc-resize-observer";
import { Content } from "antd/lib/layout/layout";
import { useState, useEffect } from "react";
import { PathActionType } from "../../common/object/reducer/PathReducer";
import { store } from "../../common/store";
import ReportPathNavigate from "../report_path_navigate/ReportPathNavigate";
import ResultTree from "../result_tree/ResultTree";
import ReportFrame from "./ReportFrame";
import ResultObject from "../../common/object/ResultObject";

declare const report: ResultObject;

export default function ReportViewer() {
  const [collapse, setCollapse] = useState<boolean>(false);
  const [height, setHeight] = useState<number>(0);

  useEffect(() => {
    
    if (report !== undefined && report.results[0] !== undefined) {
      store.dispatch({
        type: PathActionType.APPEND_PATH,
        content: {
          fullIndexToPath: [0],
          resultObject: report.results[0],
        },
      });
    }
  }, []);

  const collapseTree = () => {
    setCollapse(!collapse);
  };

  return (
    <Layout className="report-layout">
      <Content style={{ padding: "0 0 0 0" }}>
        <Row className="full-size-div">
          <Col span={collapse ? 1 : 4} style={{ height: "100%" }}>
            <ResizeObserver
              onResize={(resize) => {
                setHeight(resize.height);
              }}
            >
              <div
                className="full-size-div"
                style={{ display: "flex", flexDirection: "column" }}
              >
                <ResultTree
                  resultObj={report}
                  height={height - 30}
                  isCollapse={collapse}
                  setCollapse={setCollapse}
                />
                <Button
                  block
                  style={{ height: "30px", backgroundColor: "#d9d9d9" }}
                  type="text"
                  icon={collapse ? <RightOutlined /> : <LeftOutlined />}
                  onClick={collapseTree}
                />
              </div>
            </ResizeObserver>
          </Col>
          <Col span={collapse ? 23 : 20} style={{ height: "100%" }}>
            <Layout.Content className="report-content-area full-size-div">
              <Row>
                <Col span={24}>
                  <ReportPathNavigate />
                </Col>
              </Row>
              <Row>
                <Col span={24}>
                  <ReportFrame />
                </Col>
              </Row>
            </Layout.Content>
          </Col>
        </Row>
      </Content>
    </Layout>
  );
}
