/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from "react";
import { useSelector } from 'react-redux';
import { PathActionType } from '../../../common/object/reducer/PathReducer';
import ReportObject from "../../../common/object/ReportObject";
import ResultObject from '../../../common/object/ResultObject';
import { checkResultVisited } from '../../../common/object/selector/PathSelector';
import { store } from '../../../common/store';


interface TableCellTestNameTitleProp {
  indexToPath: number[];
  resultObject: ResultObject | ReportObject;
  preferDisplayName?: string;
  text: string;
}

export default function TableCellTestNameTitle(prop: TableCellTestNameTitleProp) {
  const isVisited = useSelector(checkResultVisited(prop.indexToPath.join('-')));
  return (
    <a
      style={isVisited ? { color: '#69c0ff' } : { fontWeight: 'bold' }}
      onClick={() => {
        store.dispatch({
          type: PathActionType.APPEND_PATH,
          content: {
            fullIndexToPath: prop.indexToPath,
            resultObject: prop.resultObject,
            preferDisplayName: prop.preferDisplayName,
          },
        });
      }}
    >
      {prop.text}
    </a>
  );
}
