import { Table, Typography } from "antd";
import { useMemo } from "react";
import { PlatformObject } from "../../common/object/PlatformObject";
import ReportObject from "../../common/object/ReportObject";
import ReportStatus from "../../common/object/ReportStatus";
import SummaryTestCaseTableRow from "../../common/object/table_row_data/SummaryTestCaseTableRow";
import { getIconForPlatform, getColorForReportStatus, getIconForReport2Status, convertTimeToString } from "../../common/utilities/ReportUtilities";
import TestCaseRoundComponent2 from "./TestCaseRoundComponent2";

const { Text, Paragraph } = Typography;

interface TestCaseSummaryTableProp {
  caseResults: ReportObject[];
  pathToResult: number[];
}

export default function TestCaseSummaryTable2(prop: TestCaseSummaryTableProp) {
  const [data, nameFilter, statusFilter, platformFilter] = useMemo(() => {
    const data1: SummaryTestCaseTableRow[] = [];
    const nameFilter1: { text: string; value: string }[] = [];
    const nameSet = new Set<string>();
    const statusSet = new Set<ReportStatus>();
    const statusFilter1: { text: string; value: string }[] = [];
    const platformSet = new Set<string>();
    const platformFilter1: { text: string; value: string }[] = [];

    prop.caseResults.forEach((caseRs, index) => {
      if (caseRs.status === ReportStatus.NOTEXECUTE) return;

      data1.push(new SummaryTestCaseTableRow(index + 1, caseRs));
      nameSet.add(caseRs.name);
      statusSet.add(caseRs.status);
      platformSet.add((caseRs.meta.platform as PlatformObject).displayName);
    });

    nameSet.forEach((name) => {
      nameFilter1.push({ text: name, value: name });
    });
    statusSet.forEach((status) => {
      statusFilter1.push({ text: status, value: status });
    });
    platformSet.forEach((platform) => {
      platformFilter1.push({ text: platform, value: platform });
    });
    return [data1, nameFilter1, statusFilter1, platformFilter1];
  }, [prop.caseResults]);

  const columns = [
    {
      title: "#",
      dataIndex: "key",
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.key - b.key,
    },
    {
      title: "Test Case",
      dataIndex: "caseName",
      filters: nameFilter,
      onFilter: (value: any, record: SummaryTestCaseTableRow) =>
        record.caseName === value,
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.caseName.localeCompare(b.caseName),
      render: (text: string) => {
        return <div>{text}</div>;
      },
    },
    {
      title: "Platform",
      dataIndex: "platformName",
      filters: platformFilter,
      onFilter: (value: any, record: SummaryTestCaseTableRow) =>
        record.platformName === value,
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.platformName.localeCompare(b.status),
      render: (text: string, row: SummaryTestCaseTableRow) => {
        return (
          <Paragraph
            style={{
              marginBottom: 0,
            }}
          >
            {getIconForPlatform(
              ((row.resultObj as ReportObject).meta.platform as PlatformObject)
                .type,
              14
            )}
            <Text
              style={{
                marginLeft: "5px",
              }}
            >
              {text}
            </Text>
          </Paragraph>
        );
      },
    },
    {
      title: "Status",
      dataIndex: "status",
      filters: statusFilter,
      onFilter: (value: any, record: SummaryTestCaseTableRow) =>
        record.status === value,
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.status.localeCompare(b.status),
      render: (text: string, row: SummaryTestCaseTableRow) => {
        return (
          <Paragraph
            strong
            style={{
              marginBottom: 0,
              color: getColorForReportStatus(row.status as ReportStatus),
            }}
          >
            {getIconForReport2Status(row.status as ReportStatus, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color: getColorForReportStatus(row.status as ReportStatus),
              }}
            >
              {text}
            </Text>
          </Paragraph>
        );
      },
    },
    {
      title: "Execution Time",
      dataIndex: "executeTime",
      sorter: (a: SummaryTestCaseTableRow, b: SummaryTestCaseTableRow) =>
        a.executeTime - b.executeTime,
      render: (_text: string, row: SummaryTestCaseTableRow) => {
        return convertTimeToString(row.executeTime);
      },
    },
  ];

  return (
    <Table
      columns={columns}
      dataSource={data}
      pagination={{ hideOnSinglePage: true }}
      expandable={{
        expandedRowRender: (record) => (
          <TestCaseRoundComponent2
            row={record}
            pathToResult={prop.pathToResult}
          />
        ),
      }}
      className="custom-table report-table"
    />
  );
}
