import {
  TagOutlined,
  CalendarOutlined,
  ClockCircleOutlined,
} from "@ant-design/icons";
import { Row, Col, Card } from "antd";
import React, { useMemo } from "react";
import ResultObject from "../../common/object/ResultObject";
import {
  getColorForStatus,
  getIconForReportStatus,
  convertTimeToString,
} from "../../common/utilities/ReportUtilities";
import ResultInfoCard from "../common/result_info_card/ResultInfoCard";
import PassFailChart from "../common/pass_fail_chart/PassFailChart";
import { ResultStatus } from "../../common/object/ResultStatus";
import TestCaseSummaryTable from "../test_case_summary_table/TestCaseSummaryTable";
import moment from "moment";

interface TestSuiteReportProp {
  suiteResult: ResultObject;
  pathToResult: number[];
}

export default function TestSuiteReport(prop: TestSuiteReportProp) {
  const [passTcCount, failTcCount, stopTcCount, passRsCount, failRsCount, stopRsCount] = useMemo(() => {
    let passTcCount = 0;
    let failTcCount = 0;
    let stopTcCount = 0;

    let passRoundCount = 0;
    let failRoundCount = 0;
    let stopRoundCount = 0;

    prop.suiteResult.results.forEach((caseRs) => {
      switch (caseRs.status) {
        case ResultStatus.PASSED:
          passTcCount += 1;
          break;
        case ResultStatus.FAILED:
          failTcCount += 1;
          break;
        case ResultStatus.STOPPED:
          stopTcCount += 1;
          break;
        default:
          break;
      }
      caseRs.results.forEach((r) => {
        switch (r.status) {
          case ResultStatus.PASSED:
            passRoundCount += 1;
            break;
          case ResultStatus.FAILED:
            failRoundCount += 1;
            break;
          case ResultStatus.STOPPED:
            stopRoundCount += 1;
            break;
          default:
            break;
        }
      });
    });

    return [passTcCount, failTcCount, stopTcCount, passRoundCount, failRoundCount, stopRoundCount];

  }, [prop.suiteResult.results]);

  const startTime = new Date(prop.suiteResult.startTimestamp);
  const start = moment(startTime);
  const startTimeStr = start.format("YYYY/MM/DD HH:mm:ss");

  return (
    <Row>
      <Col span={24}>
        <Row className="test-report-info-row">
          <Col span={8} className="test-report-info">
            <Row className="test-report-info-card">
              <ResultInfoCard
                title="Test Suite:"
                value={prop.suiteResult.name}
                color="#003153"
                icon={<TagOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card">
              <ResultInfoCard
                title="Result:"
                value={prop.suiteResult.status}
                color={getColorForStatus(prop.suiteResult.status)}
                icon={getIconForReportStatus(prop.suiteResult.status, 24)}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card">
              <ResultInfoCard
                title="Started At:"
                value={startTimeStr}
                color="#dcc713"
                icon={<CalendarOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card">
              <ResultInfoCard
                title="Execution Time:"
                value={convertTimeToString(prop.suiteResult.executeTime)}
                color="#0fa9ea"
                icon={<ClockCircleOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
          </Col>
          <Col span={16}>
          <Row style={{ height: '100%' }}>
              <Col span={12}>
                <Card
                  className="report-chart-card report-card-border"
                  style={{
                    marginLeft: '10px',
                    height: '100%',
                  }}
                >
                  <PassFailChart title="Test Case Pass/Fail" pass={passTcCount} fail={failTcCount} stop={stopTcCount} />
                </Card>
              </Col>
              <Col span={12}>
                <Card
                  className="report-chart-card report-card-border"
                  style={{
                    marginLeft: '10px',
                    height: '100%',
                  }}
                >
                  <PassFailChart title="Rounds Pass/Fail" pass={passRsCount} fail={failRsCount} stop={stopRsCount} />
                </Card>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card className="report-card-border report-content">
              <TestCaseSummaryTable suiteResult={prop.suiteResult} pathToResult={prop.pathToResult} />
            </Card>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
