import React from "react";
import { Descriptions } from "antd";

interface DataSliceDetailProp {
  sliceJson: string;
}

export default function DataSliceDetail(prop: DataSliceDetailProp) {
  let dataSlice: any;

  try {
    dataSlice = JSON.parse(prop.sliceJson);
  } catch (e) {
    console.log(e);
  }

  return dataSlice ? (
    <Descriptions column={1} bordered>
      {(dataSlice.slice.columns as string[]).map((col, index) => (
        <Descriptions.Item label={col} key={col}>
          {dataSlice.slice.dataTable[0][index]}
        </Descriptions.Item>
      ))}
    </Descriptions>
  ) : null;
}
