import React from "react";
import { Descriptions, Typography } from "antd";

const { Paragraph } = Typography;

interface RequestObjectDetailProp {
  objJson: string;
}

export default function RequestObjectDetail(prop: RequestObjectDetailProp) {
  let requestObj: any;

  try {
    requestObj = JSON.parse(prop.objJson);
  } catch (e) {
    console.log(e);
  }

  if (requestObj === undefined) return null;

  const headerKeys = Object.keys(requestObj.headerMap);
  return (
    <>
      <Descriptions title="Request Info" column={1} bordered>
        <Descriptions.Item label="URL">
          <Paragraph style={{ whiteSpace: 'pre-wrap', margin: 0 }} copyable ellipsis={{ expandable: true, symbol: "See more", rows: 1 }}>
            {requestObj.url}
          </Paragraph>
        </Descriptions.Item>
        <Descriptions.Item label="Method">
          {requestObj.requestOption}
        </Descriptions.Item>
        <Descriptions.Item label="Body">
          <Paragraph style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} copyable ellipsis={{ expandable: true, symbol: 'See more', rows: 1 }}>
            {requestObj.body}
          </Paragraph>
        </Descriptions.Item>
      </Descriptions>
      <Descriptions
        title="Request Header"
        style={{ marginTop: "20px" }}
        column={1}
        bordered
      >
        {headerKeys.map((key) => (
          <Descriptions.Item label={key} key={key}>
            <Paragraph style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }} copyable ellipsis={{ expandable: true, symbol: 'See more', rows: 1 }}>
              {requestObj.headerMap[key]}
            </Paragraph>
          </Descriptions.Item>
        ))}
      </Descriptions>
    </>
  );
}
