import { Descriptions } from 'antd';

type ValueMapDetailProp = {
  valueMapJson: string;
};

export default function ValueMapDetail(prop: ValueMapDetailProp) {
  let valueMap: Record<string, string> = {};

  try {
    valueMap = JSON.parse(prop.valueMapJson);
  } catch (e) {
    console.log(e);
  }

  return (
    <Descriptions column={1} bordered>
      {Object.entries(valueMap).map(([key, value]) => (
        <Descriptions.Item label={key} key={key}>
          {value}
        </Descriptions.Item>
      ))}
    </Descriptions>
  );
}
