/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { Breadcrumb } from "antd";
import { useSelector } from "react-redux";
import { getPathList } from "../../common/object/selector/PathSelector";
import { store } from "../../common/store";
import { PathActionType } from "../../common/object/reducer/PathReducer";

export default function ReportPathNavigate() {
  const pathList = useSelector(getPathList);

  const handlePathClick = (resultId: string) => {
    store.dispatch({
      type: PathActionType.MOVE_TO_EXIST_PATH,
      content: resultId,
    });
  };

  return (
    <Breadcrumb className="navigate-bar report-card-border">
      {pathList.map((path) => {
        return (
          <Breadcrumb.Item
            key={path.resourceId}
            onClick={() => {
              handlePathClick(path.resourceId);
            }}
          >
            <a>{path.displayName}</a>
          </Breadcrumb.Item>
        );
      })}
    </Breadcrumb>
  );
}
