import { Typography } from 'antd';
import StepResultTableRow from '../../common/object/table_row_data/StepResultTableRow';
import ReportObject from '../../common/object/ReportObject';
import { convertTimeToString, getColorForReportStatus, getIconForReport2Status } from '../../common/utilities/ReportUtilities';

export default class SubResultTableColRenderSupport {
  col: string;

  constructor(col: string) {
    this.col = col;
  }

  getColRender() {
    switch (this.col) {
      case 'Name':
        return (_text: string, row: StepResultTableRow) => {
          return <Typography.Text>{row.result.name}</Typography.Text>;
        };
      case 'Status':
        return (_text: string, row: StepResultTableRow) => {
          const report = row.result as ReportObject;
          return (
            <Typography.Paragraph
              strong
              style={{
                marginBottom: 0,
                color: getColorForReportStatus(report.status),
              }}
            >
              {getIconForReport2Status(report.status, 14)}
              <Typography.Text
                style={{
                  marginLeft: '5px',
                  color: getColorForReportStatus(report.status),
                }}
              >
                {row.result.status}
              </Typography.Text>
            </Typography.Paragraph>
          );
        };
      case 'Duration':
        return (_text: string, row: StepResultTableRow) => {
          const report = row.result as ReportObject;
          return convertTimeToString(report.meta.execute_time as number);
        };
      default:
        return undefined;
    }
  }
}
