import React, { useMemo } from "react";
import { Card, Col, Row } from "antd";
import {
  TagOutlined,
  CalendarOutlined,
  ClockCircleOutlined,
} from "@ant-design/icons";

import ResultObject from "../../common/object/ResultObject";
import ResultInfoCard from "../common/result_info_card/ResultInfoCard";
import {
  convertTimeToString,
  getColorForStatus,
  getIconForReportStatus,
} from "../../common/utilities/ReportUtilities";
import { ResultStatus } from "../../common/object/ResultStatus";
import PassFailChart from "../common/pass_fail_chart/PassFailChart";
import TestSuiteSummaryTable from "../test_suite_summary_table/TestSuiteSummaryTable";
import moment from "moment";

interface TestPlanReportProp {
  planResult: ResultObject;
  pathToResult: number[];
}

export default function TestPlanReport(prop: TestPlanReportProp) {
  const [
    passSuiteCount,
    failSuiteCount,
    stopSuiteCount,
    passTcCount,
    failTcCount,
    stopTcCount,
  ] = useMemo(() => {
    let passSuiteCount = 0;
    let failSuiteCount = 0;
    let stopSuiteCount = 0;
    let passTcCount = 0;
    let failTcCount = 0;
    let stopTcCount = 0;

    prop.planResult.results.forEach((suiteRs) => {
      switch (suiteRs.status) {
        case ResultStatus.PASSED:
          passSuiteCount += 1;
          break;
        case ResultStatus.FAILED:
          failSuiteCount += 1;
          break;
        case ResultStatus.STOPPED:
          stopSuiteCount += 1;
          break;
        default:
          break;
      }
      suiteRs.results.forEach((caseRs) => {
        switch (caseRs.status) {
          case ResultStatus.PASSED:
            passTcCount += 1;
            break;
          case ResultStatus.FAILED:
            failTcCount += 1;
            break;
          case ResultStatus.STOPPED:
            stopTcCount += 1;
            break;
          default:
            break;
        }
      });
    });

    return [
      passSuiteCount,
      failSuiteCount,
      stopSuiteCount,
      passTcCount,
      failTcCount,
      stopTcCount,
    ];
  }, [prop.planResult.results]);

  const startTime = new Date(prop.planResult.startTimestamp);
  const start = moment(startTime);
  const startTimeStr = start.format("YYYY/MM/DD HH:mm:ss");

  return (
    <Row>
      <Col span={24}>
        <Row>
          <Col span={8} className="test-report-info">
            <Row className="test-report-info-card">
              <ResultInfoCard
                title="Test Plan:"
                value={prop.planResult.name}
                color="#003153"
                icon={<TagOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card"
            >
              <ResultInfoCard
                title="Result:"
                value={prop.planResult.status}
                color={getColorForStatus(prop.planResult.status)}
                icon={getIconForReportStatus(prop.planResult.status, 24)}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card"
            >
              <ResultInfoCard
                title="Started At:"
                value={startTimeStr}
                color="#dcc713"
                icon={<CalendarOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card"
            >
              <ResultInfoCard
                title="Execution Time:"
                value={convertTimeToString(prop.planResult.executeTime)}
                color="#0fa9ea"
                icon={<ClockCircleOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
          </Col>
          <Col span={16}>
            <Row style={{ height: "100%" }}>
              <Col span={12}>
                <Card
                  className="report-chart-card report-card-border"
                  style={{ marginLeft: "10px", height: "100%" }}
                >
                  <PassFailChart
                    title="Test Suite Pass/Fail"
                    pass={passSuiteCount}
                    fail={failSuiteCount}
                    stop={stopSuiteCount}
                  />
                </Card>
              </Col>
              <Col span={12}>
                <Card
                  className="report-chart-card report-card-border"
                  style={{ marginLeft: "10px", height: "100%" }}
                >
                  <PassFailChart
                    title="Test Case Pass/Fail"
                    pass={passTcCount}
                    fail={failTcCount}
                    stop={stopTcCount}
                  />
                </Card>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card className="report-card-border report-content">
              <TestSuiteSummaryTable planResult={prop.planResult} pathToResult={prop.pathToResult}/>
            </Card>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
