import React from "react";
import { Typography } from "antd";

import StepResultTableRow from "../../common/object/table_row_data/StepResultTableRow";
import { StepResultTableColumnName } from "./StepResultTableColumnName";
import {
  convertTimeToString,
  getColorForStatus,
  getIconForPlatform,
  getIconForReportStatus,
} from "../../common/utilities/ReportUtilities";
import ResultParamCell from "./ResultParamCell";
import { ResultType } from "../../common/object/ResultType";
import { Platform } from "../../common/object/Platform";
import ResultObject from "../../common/object/ResultObject";

const { Paragraph, Text } = Typography;

export default class StepResultTableCellRenderSupport {
  col: StepResultTableColumnName;

  constructor(col: StepResultTableColumnName) {
    this.col = col;
  }

  getCellRender(row: StepResultTableRow) {
    const rs = row.result as ResultObject;
    let color:string;
    switch (this.col) {
      case StepResultTableColumnName.NAME: {
        let component: JSX.Element = <Typography.Text>{`${row.key} ${row.result.name}`}</Typography.Text>;
        switch (row.result.typeName) {
          case ResultType.FORLOOP:
          case ResultType.FORLOOPINDEX:
            component = (
              <Typography.Text style={{ color: '#d48806' }}>
                {`${row.key} ${row.result.name}`} ({row.result.results.length} row(s))
              </Typography.Text>
            );
            break;
          case ResultType.IF:
          case ResultType.BREAK:
          case ResultType.CONTINUE:
            component = <Typography.Text style={{ color: '#d48806' }}>{`${row.key} ${row.result.name}`}</Typography.Text>;
            break;
          case ResultType.KEYWORDGROUP:
            component = <Typography.Text style={{ color: '#87d068' }}>{`${row.key} ${row.result.name}`}</Typography.Text>;
            break;
          case ResultType.CUSTOMKEYWORD:
            component = <Typography.Text style={{ color: '#d4b106' }}>{`${row.key} ${row.result.name}`}</Typography.Text>;
            break;
          default:
            break;
        }
        return component;
      }
      case StepResultTableColumnName.PLATFORM:
        switch (rs.platform.type) {
          case Platform.WEB_PLATFORM:
          case Platform.CHROME_PLATFORM:
          case Platform.FIREFOX_PLATFORM:
          case Platform.SAFARI_PLATFORM:
          case Platform.EDGE_PLATFORM:
          case Platform.IE_PLATFORM:
            color = '#722ed1';
            break;
          case Platform.MOBILE_PLATFORM:
          case Platform.IOS_PLATFORM:
          case Platform.ANDROID_PLATFORM:
            color = '#5EB0E5';
            break;
          case Platform.WINDOWS_PLATFORM:
            color = '#008080';
            break;
          default:
            color = 'rgba(0, 0, 0, 0.85)';
            break;
        }
        return (
          <Paragraph
            style={{
              marginBottom: 0,
            }}
          >
            {getIconForPlatform(rs.platform.type, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color
              }}
            >
              {rs.platform.displayName ? rs.platform.displayName : rs.platform.type}
            </Text>
          </Paragraph>
        );
      case StepResultTableColumnName.PARAMETER:
        return <ResultParamCell params={rs.parameter} />;
      case StepResultTableColumnName.STATUS:
        return (
          <Paragraph
            strong
            style={{
              marginBottom: 0,
              color: getColorForStatus(rs.status),
            }}
          >
            {getIconForReportStatus(rs.status, 14)}
            <Text
              style={{
                marginLeft: "5px",
                color: getColorForStatus(rs.status),
              }}
            >
              {row.result.status}
            </Text>
          </Paragraph>
        );
      case StepResultTableColumnName.TIME:
        return convertTimeToString(rs.executeTime);
      default:
        return "";
    }
  }
}
