import StepResultTableRow from '../../common/object/table_row_data/StepResultTableRow';
import TestFlexTableCellRenderSupport2 from './TestFlexTableCellRenderSupport2';

export default class TestFlexTableColRenderSupport2 {
  col: string;
  cellRenderSupport: TestFlexTableCellRenderSupport2;

  constructor(col: string) {
    this.col = col;
    this.cellRenderSupport = new TestFlexTableCellRenderSupport2(col);
  }

  getColRender() {
    return (_text: string, row: StepResultTableRow) => {
      return this.cellRenderSupport.getCellRender(row);
    };
  }
}
