import React from "react";
import { Doughnut } from "react-chartjs-2";

interface PassFailChartProp {
  title: string;
  pass: number;
  fail: number;
  stop: number;
}

export default function PassFailChart(prop: PassFailChartProp) {
  const data = {
    labels: ["Pass", "Fail", "Stop"],
    datasets: [
      {
        data: [prop.pass, prop.fail, prop.stop],
        backgroundColor: ["#09af00", "#f01e23", "#fea101"],
        hoverBackgroundColor: ["#09af00", "#f01e23", "#fea101"],
      },
    ],
  };

  const chartOption = {
    title: {
      display: true,
      text: prop.title,
      fontSize: 24,
    },
    maintainAspectRatio: false,
  };

  return <Doughnut data={data} options={chartOption} height={100} />;
}
