import React from 'react';
import { Typography } from 'antd';
export default class UIObjectElementTreeNode {
  key: string;
  locatorList: any[];
  title: string | JSX.Element;
  children?: UIObjectElementTreeNode[];
  selectable: boolean;

  constructor(element: any, isShadow: boolean, key: string) {
    this.key = key;
    if (isShadow) {
      this.selectable = false;
      this.title = <Typography.Text className="shadow-element-tree">shadow-root</Typography.Text>;
      this.children = [new UIObjectElementTreeNode(element, false, `${key}.1`)];
      this.locatorList = [];
    } else {
      this.selectable = true;
      this.title = 'Element';
      this.locatorList = element.locatorList;
      if (element.childObj) {
        this.children = [new UIObjectElementTreeNode(element.childObj, element.childObj.inShadowRoot, `${key}.1`)];
      }
    }
  }
}
