import { Descriptions, Divider, Table } from "antd";
import React from "react";

interface DataTableDetailProp {
  tableJson: string;
}

interface TableDataRow {
  key: number;
  row: Record<string, any>;
}

export default function DataTableDetail(prop: DataTableDetailProp) {
  let dataTable: any;
  try {
    dataTable = JSON.parse(prop.tableJson);
  } catch (e) {
    console.log(e);
  }

  if (dataTable === undefined) return null;

  const columns = (dataTable.columns as string[]).map((col) => {
    return {
      title: col,
      dataIndex: col,
      ellipsis: true,
      render: (text: string, row: TableDataRow) => {
        return row.row[col];
      },
    };
  });

  const data: TableDataRow[] = [];
  (dataTable.dataTable as any[][]).forEach((row, index) => {
    const rowData: TableDataRow = {
      key: index + 1,
      row: {},
    };
    (dataTable.columns as string[]).forEach((col, idx) => {
      rowData.row[col] = row[idx];
    });
    data.push(rowData);
  });

  return (
    <>
      <Descriptions title="Table Info" column={1} bordered>
        <Descriptions.Item label="Number of column">{dataTable.columnCount}</Descriptions.Item>
        <Descriptions.Item label="Number of row">{dataTable.rowCount}</Descriptions.Item>
      </Descriptions>
      <Divider>Sample Table</Divider>
      <Table columns={columns} dataSource={data} pagination={{ hideOnSinglePage: true }} className="report-table" />
    </>
  );
}
