import React from "react";
import { Descriptions, Typography } from "antd";

const { Paragraph } = Typography;

interface DatabaseQueryDetailProp {
  objJson: string;
}

export default function DatabaseQueryDetail(prop: DatabaseQueryDetailProp) {
  let queryObj: any;
  try {
    queryObj = JSON.parse(prop.objJson);
  } catch (e) {
    console.log(e);
  }

  return queryObj ? (
    <Descriptions column={1} bordered>
      <Descriptions.Item label="Connection String">
        <Paragraph style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }}>{queryObj.connectionString}</Paragraph>
      </Descriptions.Item>
      <Descriptions.Item label="Query">
        <Paragraph style={{ whiteSpace: 'pre-wrap', marginBottom: 0 }}>{queryObj.queryString}</Paragraph>
      </Descriptions.Item>
    </Descriptions>
  ) : null;
}
