import { Card, Col, Collapse, Divider, Row, Typography } from "antd";
import ReportObject from "../../common/object/ReportObject";
import StepResultTable2 from "../step_result_table2/StepResultTable2";
import ReportStatus from "../../common/object/ReportStatus";
import { getColorForReportStatus, getIconForReport2Status } from "../../common/utilities/ReportUtilities";

interface StepResultWrapperProp {
  tcResult: ReportObject;
}

export default function StepResultWrapper2(prop: StepResultWrapperProp) {
  if (prop.tcResult.results === undefined || prop.tcResult.results.length === 0) return null;

  const setUpResult = (prop.tcResult.results as ReportObject[])[0];
  const mainResult = (prop.tcResult.results as ReportObject[])[1];
  const tearResult = (prop.tcResult.results as ReportObject[])[2];

  const generateTitle = (reportStatus: ReportStatus, title: string) => {
    return (
      <Divider className="report-content-divider-collapse">
        <Typography.Text style={{ marginRight: '5px' }}>{title}</Typography.Text>
        <Typography.Text style={{ color: getColorForReportStatus(reportStatus) }}>{getIconForReport2Status(reportStatus, 14)}</Typography.Text>
      </Divider>
    );
  };

  return (
    <>
      {setUpResult && setUpResult.results !== undefined && setUpResult.results.length > 0 ? (
        <Row>
          <Col span={24}>
            <Card className="report-info-card report-card-border">
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel className="report-content-collapse-header" header={generateTitle(setUpResult.status, 'Set Up')} key="1">
                  <StepResultTable2 stepResultList={setUpResult.results} stepIdx="1" />
                </Collapse.Panel>
              </Collapse>
            </Card>
          </Col>
        </Row>
      ) : null}
      {mainResult && mainResult.results !== undefined && mainResult.results.length > 0 ? (
        <Row>
          <Col span={24}>
            <Card className="report-info-card report-card-border">
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel className="report-content-collapse-header" header={generateTitle(mainResult.status, 'Main Execution')} key="1">
                  <StepResultTable2 stepResultList={mainResult.results} stepIdx="2" />
                </Collapse.Panel>
              </Collapse>
            </Card>
          </Col>
        </Row>
      ) : null}
      {tearResult && tearResult.results !== undefined && tearResult.results.length > 0 ? (
        <Row>
          <Col span={24}>
            <Card className="report-info-card report-card-border">
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel className="report-content-collapse-header" header={generateTitle(tearResult.status, 'Tear Down')} key="1">
                  <StepResultTable2 stepResultList={tearResult.results} stepIdx="3" />
                </Collapse.Panel>
              </Collapse>
            </Card>
          </Col>
        </Row>
      ) : null}
    </>
  );
}
