import { useMemo } from "react";
import { Row, Col, Card } from "antd";
import {
  TagOutlined,
  CalendarOutlined,
  ClockCircleOutlined,
} from "@ant-design/icons";
import moment from "moment";

import ReportObject from "../../common/object/ReportObject";
import ReportStatus from "../../common/object/ReportStatus";
import ReportType from "../../common/object/ReportType";
import { getColorForReportStatus, getIconForReport2Status, convertTimeToString } from "../../common/utilities/ReportUtilities";
import PassFailChart from "../common/pass_fail_chart/PassFailChart";
import ResultInfoCard from "../common/result_info_card/ResultInfoCard";
import TestCaseSummaryTable2 from "../test_case_summary_table2/TestCaseSummaryTable2";

interface TestSuiteReportProp {
  suiteResult: ReportObject;
  pathToResult: number[];
}

function randomId(length: number) {
  const salt = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let id = '';
  for (let i = 0; i < length; i++) {
    id += salt.charAt(Math.floor(Math.random() * salt.length - 1));
  }
  return id;
}

export default function TestSuiteReport2(prop: TestSuiteReportProp) {
  const convertedChildRs = useMemo(() => {
    //  Create fake tc row to group round result
    const mapping: Map<
      string,
      {
        name: string;
        platform: Record<string, string>;
        status: ReportStatus;
        rounds: ReportObject[];
        executeTime: number;
      }
    > = new Map();
    const roundIndexMapping: Map<ReportObject, number> = new Map();
    prop.suiteResult.results?.forEach((childRs, index) => {
      childRs.meta.rsIndex = index;

      roundIndexMapping.set(childRs, index);
      const groupingId = childRs.meta.groupId as string;
      const currentObj = mapping.get(groupingId);
      if (currentObj) {
        currentObj.rounds.push(childRs);
        if (
          childRs.status === ReportStatus.PASS &&
          currentObj.status === ReportStatus.NOTEXECUTE
        )
          currentObj.status = ReportStatus.PASS;
        else if (
          childRs.status === ReportStatus.FAIL &&
          currentObj.status !== ReportStatus.STOP
        )
          currentObj.status = ReportStatus.FAIL;
        else if (childRs.status === ReportStatus.STOP)
          currentObj.status = ReportStatus.STOP;

        currentObj.executeTime += childRs.meta.execute_time as number;
      } else if (groupingId === undefined) {
        mapping.set(randomId(6), { name: 'Test case not found', status: childRs.status, platform: { displayName: '' }, rounds: [], executeTime: 0 });
      } else {
        mapping.set(groupingId, {
          name: childRs.meta.tcName as string,
          status: childRs.status,
          platform: childRs.meta.platform as Record<string, string>,
          rounds: [childRs],
          executeTime: childRs.meta.execute_time as number,
        });
      }
    });

    const rs: ReportObject[] = [];
    mapping.forEach((value, key) => {
      const fakeRs: ReportObject = {
        name: value.name,
        typeName: ReportType.TESTCASE,
        status: value.status,
        results: value.rounds,
        meta: {
          execute_time: value.executeTime,
          uuid: key,
          platform: value.platform,
        },
      };
      rs.push(fakeRs);
    });
    return rs;
  }, [prop.suiteResult]);

  const [
    passTcCount,
    failTcCount,
    stopTcCount,
    passRsCount,
    failRsCount,
    stopRsCount,
  ] = useMemo(() => {
    let passTcCount1 = 0;
    let failTcCount1 = 0;
    let stopTcCount1 = 0;

    let passRoundCount = 0;
    let failRoundCount = 0;
    let stopRoundCount = 0;

    convertedChildRs.forEach((caseRs) => {
      switch (caseRs.status) {
        case ReportStatus.PASS:
          passTcCount1 += 1;
          break;
        case ReportStatus.FAIL:
          failTcCount1 += 1;
          break;
        case ReportStatus.STOP:
          stopTcCount1 += 1;
          break;
        default:
          break;
      }
      (caseRs.results as ReportObject[]).forEach((r) => {
        switch (r.status) {
          case ReportStatus.PASS:
            passRoundCount += 1;
            break;
          case ReportStatus.FAIL:
            failRoundCount += 1;
            break;
          case ReportStatus.STOP:
            stopRoundCount += 1;
            break;
          default:
            break;
        }
      });
    });

    return [
      passTcCount1,
      failTcCount1,
      stopTcCount1,
      passRoundCount,
      failRoundCount,
      stopRoundCount,
    ];
  }, [convertedChildRs]);

  const startTime = new Date(prop.suiteResult.meta.startTimeStamp as number);
  const start = moment(startTime);
  const startTimeStr = start.format("YYYY/MM/DD HH:mm:ss");

  return (
    <Row>
      <Col span={24}>
        <Row className="test-report-info-row">
          <Col span={8} className="test-report-info">
            <Row className="test-report-info-card">
              <ResultInfoCard
                title="Test Suite:"
                value={prop.suiteResult.name}
                color="#003153"
                icon={<TagOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card"
            >
              <ResultInfoCard
                title="Result:"
                value={prop.suiteResult.status}
                color={getColorForReportStatus(prop.suiteResult.status)}
                icon={getIconForReport2Status(prop.suiteResult.status, 24)}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card"
            >
              <ResultInfoCard
                title="Started At:"
                value={startTimeStr}
                color="#dcc713"
                icon={<CalendarOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
            <Row
              style={{ marginTop: "20px" }}
              className="test-report-info-card"
            >
              <ResultInfoCard
                title="Execution Time:"
                value={convertTimeToString(
                  prop.suiteResult.meta.execute_time as number
                )}
                color="#0fa9ea"
                icon={<ClockCircleOutlined style={{ fontSize: 24 }} />}
              />
            </Row>
          </Col>
          <Col span={16}>
            <Row style={{ height: "100%" }}>
              <Col span={12}>
                <Card
                  className="report-chart-card report-card-border"
                  style={{
                    marginLeft: "10px",
                    height: "100%",
                  }}
                >
                  <PassFailChart
                    title="Test Case Pass/Fail"
                    pass={passTcCount}
                    fail={failTcCount}
                    stop={stopTcCount}
                  />
                </Card>
              </Col>
              <Col span={12}>
                <Card
                  className="report-chart-card report-card-border"
                  style={{
                    marginLeft: "10px",
                    height: "100%",
                  }}
                >
                  <PassFailChart
                    title="Rounds Pass/Fail"
                    pass={passRsCount}
                    fail={failRsCount}
                    stop={stopRsCount}
                  />
                </Card>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card className="report-info-card report-card-border">
              <TestCaseSummaryTable2
                caseResults={convertedChildRs}
                pathToResult={prop.pathToResult}
              />
            </Card>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
