import React, { useEffect, useState } from "react";
import { Col, Row, Table } from "antd";

import ResultObject from "../../common/object/ResultObject";
import StepResultTableRow from "../../common/object/table_row_data/StepResultTableRow";
import StepResultTableColRenderSupport from "./StepResultTableColRenderSupport";
import { StepResultTableColumnName } from "./StepResultTableColumnName";
import ResultDetailInfoExpandedRow from "./ResultDetailInfoExpandedRow";

import TableHeader from "./TableHeader";
import TableRow from "./TableRow";
import { ResultType } from "../../common/object/ResultType";

interface StepResultTableProp {
  stepResultList: ResultObject[];
  stepIdx: string;
}

export default function StepResultTable(prop: StepResultTableProp) {
  const [columns, setColumns] = useState<any[]>([]);

  const components = {
    header: {
      cell: TableHeader,
    },
    body:{
      row: TableRow,
    }
  };

  const checkRowCanExpand = (row: StepResultTableRow) => {
    if ((row.result as ResultObject).results.length > 0) {
      return true;
    }
    if ((row.result as ResultObject).output !== undefined || ((row.result as ResultObject).failureCause !== undefined && (row.result as ResultObject).failureCause !== '') || ((row.result as ResultObject).imagePath !== undefined && (row.result as ResultObject).imagePath !== '')) {
      return true;
    }
    return false;
  };

  const generateData = (resultList: ResultObject[], prefixKey: string) => {
    const returnData: StepResultTableRow[] = [];
    let idx = 1;
    resultList.forEach((result) => {
      const isForRound = result.typeName === ResultType.FORROUND;
      if (prefixKey === '') returnData.push(new StepResultTableRow(idx.toString(), result));
      else returnData.push(new StepResultTableRow(isForRound ? `${prefixKey}[${idx}]` : `${prefixKey}.${idx}`, result));
      idx += 1;
    });
    return returnData;
  };

  const columnsTemplate = [
    {
      title: StepResultTableColumnName.NAME,
      dataIndex: "name",
      ellipsis: true,
      width: 150,
    },
    {
      title: StepResultTableColumnName.PLATFORM,
      dataIndex: "platform",
      ellipsis: true,
      width: 150,
    },
    {
      title: StepResultTableColumnName.PARAMETER,
      dataIndex: "param",
      ellipsis: true,
      width: 150,
    },
    {
      title: StepResultTableColumnName.STATUS,
      dataIndex: "status",
      ellipsis: true,
      width: 150,
    },
    {
      title: StepResultTableColumnName.TIME,
      dataIndex: "time",
      ellipsis: true,
      width: 150,
    },
  ];

  useEffect(() => {
    if (columns.length === 0) {
      setColumns(columnsTemplate);
    } else {
      setColumns(
        columnsTemplate.map((column, index) => {
          return {
            ...column,
            width: columns[index].width,
          };
        })
      );
    }
  }, [prop.stepResultList]);

  const handleResize = (index: number) => (e: any, { size }: any) => {
    const newColumn = [...columns];
    newColumn[index].width = size.width;
    setColumns(newColumn);
  };

  const finalColumn = columns.map((col, index) => {
    const colRender = new StepResultTableColRenderSupport(col.title);
    return {
      ...col,
      render: colRender.getColRender(),
      onHeaderCell: (column: any) => ({
        width: column.width,
        onResize: handleResize(index),
      }),
    };
  });

  const data = generateData(prop.stepResultList, prop.stepIdx);

  const rowExpandRender = (
    record: StepResultTableRow,
    _index: any,
    _indent: any,
    expanded: boolean
  ) => {
    if (!expanded) {
      return null;
    }

    if ((record.result as ResultObject).results.length > 0) {
      const isKwGrpStep = record.result.typeName === ResultType.KEYWORDGROUP;
      return (
        <>
          <Row>
            <Col span={24}>
              <ResultDetailInfoExpandedRow output={(record.result as ResultObject).output} failureCause={(record.result as ResultObject).failureCause} imageName={(record.result as ResultObject).imagePath} />
            </Col>
          </Row>
          <Row style={{ marginTop: "20px" }}>
            <Col span={24}>
              <StepResultTable stepResultList={(record.result as ResultObject).results} stepIdx={isKwGrpStep ? '' : record.key} />
            </Col>
          </Row>
        </>
      );
    } else {
      return (
        <Row>
          <Col span={24}>
            <ResultDetailInfoExpandedRow output={(record.result as ResultObject).output} failureCause={(record.result as ResultObject).failureCause} imageName={(record.result as ResultObject).imagePath} />
          </Col>
        </Row>
      );
    }
  };

  return (
    <Table
      columns={finalColumn}
      dataSource={data}
      expandable={{
        expandedRowRender: rowExpandRender,
        rowExpandable: checkRowCanExpand,
      }}
      pagination={{ hideOnSinglePage: true }}
      components={components}
      className="report-table"
    />
  );
}
