/* eslint-disable jsx-a11y/no-static-element-interactions */
import { useSelector } from "react-redux";
import PathInfo from "../../common/object/PathInfo";
import { PathActionType } from "../../common/object/reducer/PathReducer";
import ReportObject from "../../common/object/ReportObject";
import ReportType from "../../common/object/ReportType";
import { getCurrentPath } from "../../common/object/selector/PathSelector";
import { store } from "../../common/store";
import ReportParamDetailModal from "../common/modal/report_param_detail_modal/ReportParamDetailModal";
import NotExecuteReport from "../not_execute_report/NotExecuteReport";
import TestCaseReport2 from "../test_case_report2/TestCaseReport2";
import TestFlexReport2 from "../test_flex_report2/TestFlexReport2";
import TestSuiteReport2 from "../test_suite_report2/TestSuiteReport2";

declare const report: ReportObject;

export default function ReportFrame2() {
  const currentPath = useSelector(getCurrentPath);
  let children = null;
  if (currentPath) {
    let currentResult = report;
    (currentPath as PathInfo).pathDirection.forEach((idx) => {
      currentResult = (currentResult.results as ReportObject[])[idx];
    });
    if (currentResult !== undefined) {
      switch (currentResult.typeName) {
        case ReportType.TESTSUITE:
        case ReportType.FLEX_TS:
          children = (
            <TestSuiteReport2
              suiteResult={currentResult}
              pathToResult={currentPath.pathDirection}
            />
          );
          break;
        case ReportType.TESTCASE:
        case ReportType.JMTESTCASE:
        case ReportType.FLEX_TC:
          children = (
            <TestCaseReport2
              caseResult={currentResult}
            />
          );
          break;
        case ReportType.TESTFLEX:
          children = <TestFlexReport2 flexResult={currentResult} />;
          break;
        case ReportType.NOTEXECUTE:
          children = <NotExecuteReport result={currentResult} />;
          break;
        default:
          break;
      }
    }
  }

  const handleKeyPress = (e: any) => {
    if (e.key === "Backspace") {
      store.dispatch({
        type: PathActionType.GO_BACK,
        content: undefined,
      });
    }
  };

  return (
    <div tabIndex={-1} onKeyDown={handleKeyPress}>
      {children}
      <ReportParamDetailModal />
    </div>
  );
}
