import ResultObject from "../../common/object/ResultObject";
import JMeterSummaryReport from "../step_result_table/JMeterSummaryReport";
import StepResultWrapper from "../step_result_table/StepResultWrapper";



type TestCaseReportContentProp = {
  caseResult: ResultObject;
};

export default function TestCaseReportContent(prop: TestCaseReportContentProp) {
  switch (prop.caseResult.platform.type) {
    case "jmeter":
      return <JMeterSummaryReport output={prop.caseResult.output} status={prop.caseResult.status} failureCause={prop.caseResult.failureCause ? prop.caseResult.failureCause : ''} name={prop.caseResult.name} jmeterReportId={prop.caseResult.uuid} />;
    default:
      return <StepResultWrapper tcResult={prop.caseResult} />;
  }
}
