import React from "react";
import { Card, Col, Descriptions, Row, Divider, Typography } from "antd";
import moment from "moment";
import {
  CalendarOutlined,
  ClockCircleOutlined,
  TagOutlined,
} from "@ant-design/icons";

import ResultObject from "../../common/object/ResultObject";
import ResultInfoCard from "../common/result_info_card/ResultInfoCard";
import {
  convertTimeToString,
  getColorForStatus,
  getIconForReportStatus,
} from "../../common/utilities/ReportUtilities";
import TestCaseReportContent from "./TestCaseReportContent";

interface TestCaseReportProp {
  caseResult: ResultObject;
}

export default function TestCaseReport(prop: TestCaseReportProp) {
  const startTime = new Date(prop.caseResult.startTimestamp);
  const start = moment(startTime);
  const startTimeStr = start.format("YYYY/MM/DD HH:mm:ss");

  return (
    <Row>
      <Col span={24}>
        <Row>
          <Col span={12} style={{ paddingRight: "10px" }}>
            <ResultInfoCard
              title="Test Case:"
              value={prop.caseResult.name}
              color="#003153"
              icon={<TagOutlined style={{ fontSize: 24 }} />}
            />
          </Col>
          <Col span={12} style={{ paddingLeft: "10px" }}>
            <ResultInfoCard
              title="Result:"
              value={prop.caseResult.status}
              color={getColorForStatus(prop.caseResult.status)}
              icon={getIconForReportStatus(prop.caseResult.status, 24)}
            />
          </Col>
        </Row>
        <Row style={{ marginTop: "20px" }}>
          <Col span={12} style={{ paddingRight: "10px" }}>
            <ResultInfoCard
              title="Started At:"
              value={startTimeStr}
              color="#dcc713"
              icon={<CalendarOutlined style={{ fontSize: 24 }} />}
            />
          </Col>
          <Col span={12} style={{ paddingLeft: "10px" }}>
            <ResultInfoCard
              title="Execution Time:"
              value={convertTimeToString(prop.caseResult.executeTime)}
              color="#0fa9ea"
              icon={<ClockCircleOutlined style={{ fontSize: 24 }} />}
            />
          </Col>
        </Row>
        <Row>
          <Card style={{ marginTop: "20px", width: "100%" }}>
            <Divider>Input Value</Divider>
            <Descriptions className="report-input-desc" bordered style={{ margin: 0 }} column={1}>
              {prop.caseResult.parameter.map((param: any, index: number) => (
                <Descriptions.Item
                  key={`${index + 1}`}
                  label={param.paramName}>
                 {param.displayValue && (
                    <Typography.Paragraph ellipsis={{ rows: 3, expandable: true, symbol: 'See more' }} copyable>
                      {param.displayValue}
                    </Typography.Paragraph>
                  )}
                </Descriptions.Item>
              ))}
            </Descriptions>
          </Card>
        </Row>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card className="report-card-border report-content">
              <TestCaseReportContent caseResult={prop.caseResult} />
            </Card>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
