import {SearchOutlined}from '@ant-design/icons';
import { Button, Form, Input, Tree } from 'antd';
import { Key, useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { PathActionType } from '../../common/object/reducer/PathReducer';
import ReportObject from '../../common/object/ReportObject';
import ReportType from '../../common/object/ReportType';
import ResultObject from '../../common/object/ResultObject';
import { ResultType } from '../../common/object/ResultType';
import { getCurrentPath } from '../../common/object/selector/PathSelector';
import ResultTreeNode from '../../common/object/tree_data/ResultTreeNode';
import { store } from '../../common/store';
import { debounceTypingFunc, getParentKey } from '../../common/utilities/FunctionUtilities';


const { Search } = Input;
const { DirectoryTree } = Tree;

type ResultTreeProp = {
  resultObj: ResultObject | ReportObject;
  height: number;
  isCollapse:boolean;
  setCollapse:(collapse:boolean)=>void
};

export default function ResultTree(prop: ResultTreeProp) {
  const isReport2 = (prop.resultObj as ReportObject).meta !== undefined;

  const tree = useRef<any>(null);
  const [rsTree, setRsTree] = useState<ResultTreeNode[]>([]);
  const [flatRsTree, setFlatRsTree] = useState<ResultTreeNode[]>([]);
  const [filteredTree, setFilteredTree] = useState<ResultTreeNode[]>([]);
  const [collapseTree, setCollapseTree] = useState<ResultTreeNode[]>([]);
  const [searchStr, setSearchStr] = useState<string>('');
  const [selectedKey, setSelectedKey] = useState<string>('');
  const [expandKeys, setExpandKeys] = useState<string[]>([]);
  const currentPath = useSelector(getCurrentPath);

  const handleSearchType = (info: any) => {
    const func = debounceTypingFunc(() => {
      setSearchStr(info.target.value);
    }, 200);
    func();
  };

  const flattenNode = (node: ResultTreeNode, result: any[]) => {
    const clonedNode = {...node};
    clonedNode.children = [];
    clonedNode.selectable = true;
    result.push(clonedNode);
    if (expandKeys.includes(node.key) && node.children) {
      node.children.forEach((child) => {
        flattenNode(child, result);
      });
    }
  };

  useEffect(() => {
    if (rsTree.length > 0) {
      const flatTree: ResultTreeNode[] = [];
      flattenNode(rsTree[0], flatTree);
      setFlatRsTree(flatTree);
    }
  }, [rsTree]);

  useEffect(() => {
    const initObj = isReport2 ? (prop.resultObj as ReportObject) : (prop.resultObj as ResultObject).results[0];
    const initPath = isReport2 ? [] : [0];
    const treeNodes = [new ResultTreeNode('1', initObj, initPath)];
    const allKeys: string[] = [];
    const collectAllKey = (results: ResultTreeNode[]) => {
      results.forEach((result) => {
        allKeys.push(result.key);
        if (result.children) collectAllKey(result.children);
      });
    };
    collectAllKey(treeNodes);
    setRsTree(treeNodes);
    setFilteredTree(treeNodes);
    setExpandKeys(allKeys);
  }, [prop.resultObj]);

  useEffect(() => {
    //  Filter on typing, only matched result and its parent can be shown
    const getItemWithMatchedName = (item: ResultTreeNode, name: string) => {
      if (item.searchTerm.toLowerCase().includes(name.toLowerCase())) {
        if (item.children) return { ...item };
        return item;
      }
      if (item.children) {
        const clonedItem = { ...item, children: [] as ResultTreeNode[] };
        item.children.forEach((child) => {
          const matchedChild = getItemWithMatchedName(child, name);
          if (matchedChild) {
            clonedItem.children.push(matchedChild as ResultTreeNode);
          }
        });
        if (clonedItem.children.length === 0) return undefined;
        return clonedItem;
      }

      return undefined;
    };
    const filterTree = () => {
      const newTree = [] as ResultTreeNode[];
      rsTree.forEach((node) => {
        const matchedChild = getItemWithMatchedName(node, searchStr);
        if (matchedChild) {
          newTree.push(matchedChild as ResultTreeNode);
        }
      });
      return newTree;
    };
    const newTree = filterTree();
    setFilteredTree(newTree);
  }, [searchStr, rsTree]);

  useEffect(() => {
    const flattenOpened = (treeNode: ResultTreeNode[]) => {
      const result: ResultTreeNode[] = [];
      treeNode.forEach((tn) => {
        flattenNode(tn, result);
      });
      return result;
    };

    setCollapseTree(flattenOpened(filteredTree));
  }, [filteredTree, expandKeys]);

  const findResultObj = (pathToObj: number[]) => {
    if (isReport2) {
      let pointer = prop.resultObj as ReportObject;
      pathToObj.forEach((k) => {
        pointer = (pointer.results as ReportObject[])[k];
      });
      return pointer;
    }

    let pointer = prop.resultObj as ResultObject;
    pathToObj.forEach((k) => {
      pointer = pointer.results[k];
    });
    return pointer;
  };

  const findOrigNode = (key: string) => {
    const split = key.split('.');
    let pointer = rsTree;
    let rs: ResultTreeNode | undefined;
    split.forEach((idx, index) => {
      if (index === split.length - 1) {
        rs = pointer[Number.parseInt(idx, 10) - 1];
      }
      pointer = pointer[Number.parseInt(idx, 10) - 1].children as ResultTreeNode[];
    });
    return rs;
  };

  const handleSelectNode = (selectedKeys: Key[]) => {
    const toSelectNodeKey = selectedKeys[selectedKeys.length - 1].toString();
    const splt = toSelectNodeKey.split('.');
    const idxArrToKey = (arr: string[]) => {
      let rs = '';
      arr.forEach((s, idx) => {
        if (idx > 0) rs += '.';
        rs += s;
      });
      return rs;
    };

    const pathInfos: { fullIndexToPath: number[]; resultObject: ResultObject | ReportObject; preferDisplayName?: string }[] = [];
    for (let i = splt.length - 1; i > 0; i -= 1) {
      const slicedArr = splt.slice(0, i + 1);
      const keyToFind = idxArrToKey(slicedArr);
      const found = findOrigNode(keyToFind);
      if (found) {
        const obj = findResultObj(found.pathToResult);
        // eslint-disable-next-line no-continue
        if (obj === undefined) continue;

        if (!isReport2) {
          const rsObj = obj as ResultObject;
          if (rsObj.typeName === ResultType.TESTCASE) {
            const tcsNode = findOrigNode(getParentKey(keyToFind)) as ResultTreeNode;
            const tcs = findResultObj(tcsNode.pathToResult) as ResultObject;
            pathInfos.push({ resultObject: rsObj, fullIndexToPath: found.pathToResult, preferDisplayName: `${tcs.name} (${rsObj.name})` });
          } else if (rsObj.typeName !== ResultType.TESTCASESUITE) pathInfos.push({ resultObject: rsObj, fullIndexToPath: found.pathToResult });
        } else {
          const reportObj = obj as ReportObject;
          if (reportObj.typeName === ReportType.TESTCASE || reportObj.typeName === ReportType.JMTESTCASE || reportObj.typeName === ReportType.FLEX_TC) {
            pathInfos.push({ resultObject: reportObj, fullIndexToPath: found.pathToResult, preferDisplayName: `${reportObj.meta.tcName} (${reportObj.name})` });
          } else pathInfos.push({ resultObject: reportObj, fullIndexToPath: found.pathToResult });
        }
      }
    }
    pathInfos.reverse();

    store.dispatch({ type: PathActionType.SET_REPORT_PATH, content: { pathInfos } });
  };

  useEffect(() => {
    if (currentPath && rsTree.length > 0) {
      const found = flatRsTree.find((rs) => rs.pathToResult.join('-') === currentPath.pathDirection.join('-'));
      if (found) {
        const { key } = found;
        setSelectedKey(key);
        const currentExpanded = new Set<string>(expandKeys);
        let parent = getParentKey(key);
        while (parent !== '') {
          currentExpanded.add(parent);
          parent = getParentKey(parent);
        }
        if (currentExpanded.size !== expandKeys.length) setExpandKeys(Array.from(currentExpanded));
        setTimeout(() => {
          tree.current?.scrollTo({ key, offset: 150 });
        }, 200);
      }
    }
  }, [currentPath, flatRsTree]);

  const handleTreeExpand = (expandedKey: any[]) => {
    if (!prop.isCollapse) setExpandKeys(expandedKey as string[]);
  };
  const handleDoubleClick = () => {
    if (prop.isCollapse) prop.setCollapse(false);
  };

  return (
    <div className="full-size-div result-tree">
      <Form.Item style={{ height: '30px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      {!prop.isCollapse ? (
          <Search allowClear className="box-search-input" onChange={handleSearchType} />
        ) : (
          <Button
            style={{ width: '100%' }}
            icon={<SearchOutlined />}
            onClick={() => {
              prop.setCollapse(false);
            }}
          />
        )}
      </Form.Item>
      <DirectoryTree
        ref={tree}
        titleRender={(node) => {
          return prop.isCollapse ? null : node.title;
        }}
        className={prop.isCollapse ? 'result-tree-collapse' : ''}
        virtual
        height={prop.height - 74}
        treeData={!prop.isCollapse ? filteredTree : collapseTree}
        blockNode
        showIcon
        onDoubleClick={handleDoubleClick}
        onSelect={handleSelectNode}
        selectedKeys={[selectedKey]}
        expandedKeys={expandKeys}
        onExpand={handleTreeExpand}
      />
      </div>
  );
}
