export function getParentKey(key: string) {
  const splt = key.split(".");
  if (splt.length === 1) return "";
  return splt.slice(0, splt.length - 1).join(".");
}

let debounceTypingTimeout: NodeJS.Timeout | undefined;
export const debounceTypingFunc = <T extends (...args: any[]) => any>(
  callback: T,
  waitFor: number
) => {
  return (...args: Parameters<T>): ReturnType<T> => {
    let result: any;
    if (debounceTypingTimeout) {
      clearTimeout(debounceTypingTimeout);
    }
    debounceTypingTimeout = setTimeout(() => {
      result = callback(...args);
    }, waitFor);
    return result;
  };
};
