import { useEffect, useState } from 'react';
import { Col, Collapse, Divider, Row, Table, Typography } from 'antd';
import ResultDetailInfoExpandedRow from '../step_result_table/ResultDetailInfoExpandedRow';
import ReportObject from '../../common/object/ReportObject';
import StepResultTableRow from '../../common/object/table_row_data/StepResultTableRow';
import TableHeader from '../step_result_table/TableHeader';
import SubResultTableColRenderSupport from './SubResultsTableColRenderSupport';
import { ResultParam } from '../../common/object/ResultParam';

type SubResultTableProp = {
  results: ReportObject[];
};

export default function SubResultsTable(prop: SubResultTableProp) {
  const [columns, setColumns] = useState<any[]>([]);

  const generateData = (resultList: ReportObject[]) => {
    return resultList.map((rs, idx) => new StepResultTableRow(`${idx + 1}`, rs));
  };

  const checkHasInfoToShow = (result: ReportObject) => {
    if (result.meta.error || result.meta.outputResult) return true;
    return false;
  };

  const checkRowCanExpand = (row: StepResultTableRow) => {
    const resultObj = row.result as ReportObject;
    if (resultObj.subResults !== undefined && resultObj.subResults.length > 0) return true;
    if (checkHasInfoToShow(resultObj)) return true;
    return false;
  };

  const components = {
    header: {
      cell: TableHeader,
    },
  };

  const columnsTemplate = [
    {
      title: 'Name',
      dataIndex: 'name',
      ellipsis: true,
      width: 150,
    },
    {
      title: 'Status',
      dataIndex: 'status',
      ellipsis: true,
      width: 150,
    },
    {
      title: 'Duration',
      dataIndex: 'time',
      ellipsis: true,
      width: 150,
    },
  ];

  useEffect(() => {
    if (columns.length === 0) {
      setColumns(columnsTemplate);
    } else {
      setColumns(
        columnsTemplate.map((column, index) => {
          return {
            ...column,
            width: columns[index].width,
          };
        })
      );
    }
  }, [prop.results]);

  const handleResize = (index: number) => (width: any) => {
    const newColumn = [...columns];
    newColumn[index].width = width;
    setColumns(newColumn);
  };

  const finalColumn = columns.map((col, index) => {
    const colRender = new SubResultTableColRenderSupport(col.title);
    return {
      ...col,
      render: colRender.getColRender(),
      onHeaderCell: (column: any) => ({
        width: column.width,
        onResize: handleResize(index),
      }),
    };
  });

  const data = generateData(prop.results);

  const rowExpandRender = (record: StepResultTableRow, _index: any, _indent: any, expanded: boolean) => {
    if (!expanded) return null;

    const resultObj = record.result as ReportObject;

    return (
      <>
        {checkHasInfoToShow(resultObj) ? (
          <Row>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition='right'>
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Info</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <ResultDetailInfoExpandedRow output={resultObj.meta.outputResult as ResultParam | undefined} failureCause={resultObj.meta.error as string | undefined} imageName={undefined} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}

        {resultObj.subResults !== undefined && resultObj.subResults.length > 0 ? (
          <Row style={{ marginTop: '20px' }}>
            <Col span={24}>
              <Collapse defaultActiveKey="1" ghost expandIconPosition="right">
                <Collapse.Panel
                  className="report-content-collapse-header"
                  header={
                    <Divider className="report-content-divider-collapse">
                      <Typography.Text style={{ fontSize: '13px' }}>Sub Step</Typography.Text>
                    </Divider>
                  }
                  key="1"
                >
                  <SubResultsTable results={resultObj.subResults} />
                </Collapse.Panel>
              </Collapse>
            </Col>
          </Row>
        ) : null}
      </>
    );
  };

  return (
    <Table
      rowKey="key"
      columns={finalColumn}
      dataSource={data}
      expandable={{
        expandedRowRender: rowExpandRender,
        rowExpandable: checkRowCanExpand,
      }}
      pagination={{ hideOnSinglePage: true }}
      components={components}
      className="custom-table report-table"
    />
  );
}
